
import { Component, EventEmitter, Output, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { HashMap, DialogEvent } from '../../../../shared/utilities/types.interfaces';
import { BaseDirective } from '../../../../shared/globals/base.directive';
import { IRoom } from '../../../../services/data/rooms.service';

import { BehaviorSubject } from 'rxjs';
import * as dayjs from 'dayjs';

export interface RecurrenceModalData {
    space: IRoom;
    recurrence_rooms: { date: number; room: IRoom }[];
}

interface RecurrenceRoom {
    date: number;
    room: IRoom;
    loading?: boolean;
    display: string;
}

@Component({
    selector: 'recurrence-details-modal',
    templateUrl: './recurrence-details.template.html',
    styleUrls: ['./recurrence-details.styles.scss'],
})
export class BookingRecurrenceDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Selected date */
    public date: number;
    /** Selected space */
    public space: IRoom;
    /** Rooms associated to each date */
    public recurrence_rooms: RecurrenceRoom[] = [];
    /** Mapping of field errors to field name */
    public error: HashMap = {};
    /** List of error messages */
    public messages: string[] = [];
    /** Emitter for room change */
    public room$ = new BehaviorSubject<{ date: number, room: IRoom}>(null);

    constructor(
        private _dialog: MatDialog,
        @Inject(MAT_DIALOG_DATA) private _data: RecurrenceModalData,
    ) {
        super();
    }

    public ngOnInit(): void {
        this.space = this._data.space;
        const recurr_rooms = this._data.recurrence_rooms;
        this.recurrence_rooms = this.space.recurr_available.map(i => {
            const data: RecurrenceRoom = {
                date: i.date,
                display: dayjs(i.date).format('dddd, MMMM D, YYYY'),
                room: i.available ? this.space : null
            };
            const found = recurr_rooms.find(conf => conf.date === i.date);
            if (found) { data.room = found.room; }
            return data;
        });
        this.room$.subscribe(item => {
            if (!item) { return; }
            const found = this.recurrence_rooms.find(i => i.date === item.date);
            found.room = item.room;
        });
    }

    public select(item: RecurrenceRoom) {
        this.event.emit({
            reason: 'action',
            metadata: { item }
        });
    }

    public get disabled(): boolean {
        return this.recurrence_rooms.some(i => !i.room);
    }

    public save() {
        this.event.emit({ reason: 'done' });
    }
}
