import { Component, OnInit, Inject, EventEmitter, Output } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { AppService } from '../../services/app.service';
import { HashMap, DialogEvent } from '../../shared/utilities/types.interfaces';
import { IBuilding } from '../../services/data/buildings.service';
import { IRoom } from '../../services/data/rooms.service';
import { AttendeeModalComponent, AttendeeModalData } from '../attendee-modal/attendee-modal.component';
import { RoomSelectModalComponent } from '../room-select-modal/room-select-modal.component';
import { RoomFilterModalData } from '../room-filter-modal/room-filter-modal.component';

import * as dayjs from 'dayjs';

export interface BookingModalData {
    room: IRoom;
    date: number;
}

@Component({
    selector: 'booking-modal',
    templateUrl: './booking-modal.template.html',
    styleUrls: ['./booking-modal.styles.scss']
})
export class BookingModalComponent extends BaseDirective implements OnInit {
    /** Emitter for events on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /**  */
    public form_settings = { recurrence: false, hide_errors: false, edit: true };
    /**  */
    public form: HashMap = {};
    /**  */
    public error: HashMap = {};
    /**  */
    public settings: HashMap = {};
    /** Whether a booking is in progress */
    public booking: boolean;
    /** Whether the booking request if being processed */
    public processing: boolean;
    /** Active building */
    public building: IBuilding;
    /** Toggled to for update the valid state of the form */
    public validate: boolean;
    /** Whether the form fields are valid */
    public valid: boolean;
    /** Minimum number of attendees */
    public min_attendees = 0;

    constructor(
        public _dialog: MatDialog,
        public _dialog_ref: MatDialogRef<BookingModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: BookingModalData,
        public _service: AppService
    ) {
        super();
    }

    public ngOnInit() {
        this.subscription('building', this._service.Buildings.listen((bld) => {
            if (bld) { this.building = bld; }
        }));
        if (this._data.room) {
            this.form = { room: this._data.room };
        }
        if (this.form.room && this.form.room.current) {
            const bkn = this.form.room.current;
            this.form.date = dayjs(bkn.start)
                .add(bkn.duration, 'm')
                .valueOf();
        }
        if (this._data.date || !this.form.date) {
            let now = dayjs();
            now = now.minute(Math.ceil(now.minute() / 5) * 5).startOf('m');
            this.form.date = this._data.date || now.valueOf();
        }
        if (!this.form.duration) {
            this.form.duration =
                this._service.Settings.get('app.booking.default_length') || 60;
        }
        this.error = {};
        this.min_attendees = this._service.Settings.get('app.booking.min_attendees');
        this.settings = this._service.Settings.get('app.booking');
        if (!this.settings.fields || this.settings.fields.length <= 0) {
            this.book();
        }
    }

    public manageAttendees() {
        const ref = this._dialog.open<AttendeeModalComponent, AttendeeModalData>(AttendeeModalComponent, {
            data: { attendees: this.form.attendees }
        });
        ref.componentInstance.event.subscribe((event: DialogEvent) => {
            if (event.reason === 'done') {
                this.form.attendees = ref.componentInstance.attendees || [];
                this.form = JSON.parse(JSON.stringify(this.form));
            }
            ref.close();
        });
    }

    public selectSpace() {
        const ref = this._dialog.open<RoomSelectModalComponent, RoomFilterModalData>(RoomSelectModalComponent, {
            data: { spaces: this.form.room, multi: this.settings.multi_space }
        });
        ref.componentInstance.event.subscribe((event: DialogEvent) => {
            if (event.reason === 'done') {
                this.form.room = ref.componentInstance.space_list;
                this.form = JSON.parse(JSON.stringify(this.form));
            }
            ref.close();
        });
    }

    public handleFormEvent(e) {
        if (e === 'Attendees') {
            if (this.form.attendees) {
                for (const item of this.form.attendees) {
                    item.select = true;
                }
            }
            this.manageAttendees();
        } else if (e === 'Space') {
            this.selectSpace();
        }
    }

    /**
     * Start booking request
     */
    public book() {
        this.booking = true;
        this.validate = !this.validate;
        setTimeout(() => {
            if (this.valid) {
                this._service.Rooms.isAvailable(
                    this.form.room.id,
                    this.form.date,
                    this.form.duration
                ).then(
                    () => this.makeBooking(),
                    () => {
                        this.booking = false;
                        const start = dayjs(this.form.date);
                        const date = start.format('MMM Do, YYYY');
                        const end = start.add(this.form.duration, 'm');
                        this._service.notifyError(
                            `The selected space is not free from ${start.format(
                                'h:mma'
                            )} to ${end.format('h:mma')} on ${date}`
                        );
                        this.error = JSON.parse(JSON.stringify(this.error));
                    }
                );
            } else {
                this.booking = false;
            }
        }, 100);
    }

    /**
     * Make booking request
     */
    private makeBooking(): void {
        this.booking = false;
        if (this.settings.title_prefix) {
            this.form.title = this.settings.title_prefix + this.form.title;
        }
        this.processing = true;
        this._service.Bookings.book(this.form).then(
            () => {
                this._dialog_ref.close();
                this._service.navigate(`book/results`);
                this.processing = false;
            },
            () => {
                if (this.settings && this.settings.title_prefix) {
                    this.form.title = this.form.title.replace(
                        this.settings.title_prefix,
                        ''
                    );
                }
                this._service.notifyError('An error occurred while making your booking');
                this.processing = false;
            }
        );
    }
}
