import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';

import { BaseDirective } from '../../../globals/base.directive';
import { AppService } from '../../../../services/app.service';
import { CUSTOM_FIELD_REGISTER } from '../../../globals/custom-field-register';
import { GuestSettingsModalComponent } from '../../../../overlays/guest-settings-modal/guest-settings-modal.component';

@Component({
    selector: 'custom-guest-settings-field',
    templateUrl: './guest-settings-field.component.html',
    styleUrls: ['./guest-settings-field.component.scss']
})
export class CustomGuestSettingsFieldComponent extends BaseDirective implements OnInit, OnDestroy {
    constructor(
        protected _field: ADynamicFormField,
        protected _group: FormGroup,
        private service: AppService,
        private _dialog: MatDialog
    ) {
        super();
    }

    public get field(): ADynamicFormField {
        return this._field;
    }
    public get group(): FormGroup {
        return this._group;
    }

    public ngOnInit(): void {
        if (this.group) {
            if (this.group.controls.attendees) {
                this.subscription(
                    'attendees',
                    this.group.controls.attendees.valueChanges.subscribe(() => this.updateDisplay())
                );
            }
        }
        let last_value = null;
        this.subscription(
            'control',
            this.field.control.valueChanges.subscribe(value => {
                if (value !== last_value) {
                    last_value = value;
                    this.updateDisplay();
                }
            })
        );
        if (this.group.value.notify_users) {
            const notify_users = this.group.value.notify_users;
            const email = notify_users ? notify_users[0] : null;
            if (email) {
                this.service.Users.query({ q: email }).then(res => {
                    const user = res && res.length > 0 ? res[0] : null;
                    this.setValue({ ...user, email });
                });
            }
        }
        this.updateDisplay();
    }

    public updateDisplay() {
        if (this.group.controls.attendees && this.group.controls.attendees.value) {
            this.field.setDisabled(
                !(this.group.controls.attendees.value || []).find(i => i.external)
            );
        } else {
            this.field.setDisabled(true);
        }
    }

    public setGuestSettings() {
        const host = this.group.value.host;
        const user = this.field.control.value;
        if (host) {
            const ref = this._dialog.open(GuestSettingsModalComponent, { data: { host, user } });
            ref.componentInstance.event.subscribe(event => {
                if (event.reason === 'done') {
                    const greeter = event.data.greeter;
                    this.setValue(greeter.user);
                }
                ref.close();
            });
        }
    }

    public setValue(value: any) {
        (this.field.control as any).setValue(value);
    }
}

CUSTOM_FIELD_REGISTER.notify_user = CustomGuestSettingsFieldComponent;
