import { Component, OnInit, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { DialogEvent } from '../../shared/utilities/types.interfaces';
import { IUser } from '../../services/data/users.service';

export interface AttendeeModalData {
    attendees: IUser[];
}

@Component({
    selector: 'attendee-modal',
    templateUrl: './attendee-modal.template.html',
    styleUrls: ['./attendee-modal.styles.scss'],
})
export class AttendeeModalComponent extends BaseDirective implements OnInit {
    /** Emitter for events on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** List of attendees */
    public attendees: IUser[] = [];

    constructor(@Inject(MAT_DIALOG_DATA) private _data: AttendeeModalData) {
        super();
    }

    public ngOnInit() {
        this.attendees = JSON.parse(JSON.stringify(this._data.attendees || []));
    }
}
