
import { Component, ViewChild, ElementRef, Output, EventEmitter } from '@angular/core';

import { Utils } from '../../shared/utility.class';
import { BaseDirective } from '../../shared/globals/base.directive';
import { Identity, DialogEvent } from '../../shared/utilities/types.interfaces';

@Component({
    selector: 'timezone-search-modal',
    templateUrl: './timezone-search-modal.template.html',
    styleUrls: ['./timezone-search-modal.styles.scss']
})
export class TimezoneSearchModalComponent extends BaseDirective {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();

    public search_str: string;

    public filtered_timezones: Identity[] = [];

    public loading: boolean;

    @ViewChild('searchField', { static: true }) private field: ElementRef;

    public init() {
        if (!this.field) {
            return this.timeout('init', () => this.init());
        }
        this.field.nativeElement.focus();
        this.search();
    }

    public search() {
        this.filtered_timezones = [];
        this.loading = true;
        this.filtered_timezones = Utils.filter(this.search_str, [], ['name']);
        for (const timezone of this.filtered_timezones) {
            if (timezone.match_name) {
                // Format match name
                timezone.match_name = timezone.match_name.replace(/\`[a-zA-Z0-9\@\.\_]*\`/g, '<span class="highlight">$&</span>');
                timezone.match_name = timezone.match_name.replace(/\`/g, '');
            }
        }
        this.loading = false;
    }

    public select(timezone: any) {
        this.event.emit({ reason: 'action', metadata: timezone });
    }
}
