import { ComposerService } from '@acaprojects/ngx-composer';
import { Injectable } from '@angular/core';

import { IRoom } from './rooms.service';
import { IUser } from './users.service';
import { BaseService } from './base.service';
import { Utils } from '../../shared/utility.class';
import { rulesForSpace, processCateringData } from '../../shared/utilities/booking.utilities';
import { RECURRENCE_PERIODS } from '../../shared/utilities/formatting.utilities';
import { CateringOrder } from './catering/catering-order.class';

import * as dayjs from 'dayjs';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmModalData } from '../../overlays/confirm-modal/confirm-modal.component';

export interface IBooking {
    id: string;
    icaluid?: string;
    title: string;
    description: string;
    date: number;
    start_hours?: number;
    duration: number;
    locations?: string;
    organiser?: IUser;
    is_organiser?: boolean;
    attendees?: IUser[];
    booked_by?: IUser;
    room?: IRoom;
    room_list?: IRoom[];
    location_name?: string;
    display?: { [name: string]: string };
    catering?: CateringOrder[];
    visitors?: boolean;
    equipment_code?: { [id: string]: string };
    status?: 'future' | 'upcoming' | 'past' | 'in_progress';
    _status?: string;
    state?: string;
    recurrence?: IBookingRecurrence;
    space_notes: { [space_id: string]: string };
    notes: IBookingNote[];
    parking?: boolean;
    available?: boolean;
    booking_type?: { id: string; name?: string };
    notify_users?: string[];
    all_day?: boolean;
    approval_status?: { [email: string]: string };
    setup?: number;
    breakdown?: number;
    for_date?: number;
    order_id?: string;
    needs_catering: boolean;
    expected_attendees?: { [space_id: string]: number };
    spaces: string[];
    conferencing?: boolean;
    recurrence_rooms?: { date: number; room: string }[];
}

export interface IBookingRecurrence {
    period: number;
    end: number;
    start: number;
    series_id: string;
}

export interface IBookingNote {
    /** Type of note */
    type: 'equipment' | 'catering' | 'description' | 'private' | 'other';
    /** Name of the note's author */
    author: string;
    /** Contents of the note */
    message: string;
    /** Time the note was added to the booking */
    date: number;
    /** Display value for the time */
    time?: string;
    /** Space associated with the note */
    space?: string;
}

export interface IBookingSlot {
    start: number;
    end: number;
}

@Injectable({
    providedIn: 'root'
})
/**
 * Extension of BaseService - is inclusion the methods in base.service.ts
 * Some of the methods which have been common across the data services are being grouped in base service to avoid duplication of code
 * Hence, methods in base service are being used as methods included in current data service
 */
export class BookingsService extends BaseService<IBooking> {
    constructor(protected _composer: ComposerService, private _dialog: MatDialog) {
        super(_composer);
        this.model.name = 'booking';
        this.model.route = '/bookings';
        this.set('timeline', {});
        this.set('new_booking', { state: 'idle' });
        this.set('update_booking', { state: 'idle' });
    }

    public init() {
        if (
            !this.parent ||
            !this.parent.Rooms.list(true) ||
            this.parent.Rooms.list(true).length <= 0 ||
            !this.parent.ready()
        ) {
            return setTimeout(() => this.init(), 500);
        }
        if (this.parent && this.parent.Settings.get('app.schedule.enabled')) {
            this.load();
        }
    }

    /**
     * load() method is called to make HTTP request using query() method in the base service
     * Note - query() methods take query parameters as arguments.
     * There is no parameters passed in the query() here, hence the query will simple get the response of the end point created using the route
     * @param tries tries
     */
    protected load(tries: number = 0) {
        this.query();
    }

    /**
     * Create modal with a booking form
     * @param room Room to book
     */
    public bookRoom(room: IRoom) {
        if (!room) {
            return;
        }
        const today = dayjs();
        today
            .minute(Math.ceil(today.minute() / 5) * 5).startOf('m');
        const bookings = room.bookings.filter(b => dayjs(b.date).isSame(today, 'd'));
        let block = this.getNextFreeBlock(bookings);
        // In case the
        if (block && dayjs(block.end).diff(today, 'm') < 30) {
            block = this.getNextFreeBlock(bookings, block.end);
        }
        const start =
            !block || block.start < 0 ? today.valueOf() : Math.max(today.valueOf(), block.start);
        const date = dayjs(start);
        date.minute(Math.ceil(date.minute() / 5) * 5);
        let duration = 60;
        if (block && block.end >= 0) {
            const length = Math.floor(dayjs(block.end).diff(date, 'm'));
            if (length < 45) {
                duration = 30;
            } else if (length < 60) {
                duration = 45;
            }
        }
        const ref = this.parent.confirm(
            {
                title: 'Book space?',
                content: `Book meeting room ${room.name} at ${date.format(
                    'h:mm a'
                )} for ${Utils.humaniseDuration(duration)}`,
                icon: { type: 'icon', class: 'material-icons', content: 'event_available'},
                action: 'Ok'
            },
            e => {
                if (e.reason === 'done') {
                    const booking = {
                        date: date.valueOf(),
                        room: { id: room.id, name: room.name },
                        duration: duration
                    };
                    if (localStorage) {
                        localStorage.setItem('STAFF.booking_form', JSON.stringify(booking));
                    }
                    this.parent.navigate('book', { back: 'schedule' });
                }
                ref.close();
            }
        );
    }

    /**
     * Create confirm modal for booking the specified space with the given parameters
     * @param item Booking form hash map
     */
    public book(item: { [name: string]: any }) {
        return new Promise((resolve, reject) => {
            if (this.get('new_booking').state !== 'idle') {
                return reject('Another booking is in progress');
            }
            const date = dayjs(item.date);
            const ref = this.parent.confirm(this.confirmSettings('add', item), event => {
                if (event.reason === 'done') {
                    this.subjects.new_booking.next({ state: 'processing' });
                    this.add(item).then(
                        d => {
                            this.set('new_booking', { state: 'success', value: d });
                            resolve(d);
                            if (localStorage) {
                                const booking = localStorage.getItem('STAFF.booking_form');
                                localStorage.removeItem('STAFF.booking_form');
                                localStorage.setItem(
                                    'STAFF.last_booking',
                                    booking || JSON.stringify(item)
                                );
                            }
                            this.timeout(`${dayjs().unix()}`, () =>
                                this.set('new_booking', { state: 'idle' })
                            );
                        },
                        e => {
                            this.set('new_booking', { state: 'error', value: e });
                            this.timeout('new_error', () =>
                                this.set('new_booking', { state: 'idle' })
                            );
                            reject(`Error: ${e}`);
                        }
                    );
                } else {
                    reject('Cancelled by user');
                }
                ref.close();
            });
        });
    }

    /**
     * Create confirm modal for updating booking of the specified space with the given parameters
     * @param item Booking form hash map
     */
    public update(id: string, item: { [name: string]: any }) {
        return new Promise((resolve, reject) => {
            const ref = this.parent.confirm(this.confirmSettings('update', item), event => {
                if (event.reason === 'done') {
                    this.set('update_booking', { state: 'processing' });
                    this.updateItem(item.id || id, item).then(
                        d => {
                            this.set('update_booking', { state: 'success', value: d });
                            const new_item = this.processItem(d);
                            resolve(new_item);
                            if (localStorage) {
                                localStorage.removeItem('STAFF.booking_form');
                            }
                            this.parent.Rooms.replaceBooking(new_item);
                            this.query();
                            this.timeout(`${dayjs().unix()}`, () =>
                                this.set('new_booking', { state: 'idle' })
                            );
                        },
                        e => {
                            this.set('update_booking', { state: 'error', value: e });
                            this.timeout('update_error', () =>
                                this.set('update_booking', { state: 'idle' })
                            );
                            reject(e);
                        }
                    );
                    ref.close();
                } else {
                    reject('Cancelled by user');
                }
                ref.close();
            });
        });
    }

    public accept(id: string, fields?: { [name: string]: any }) {
        return this.task(id, 'accept', fields);
    }

    /**
     * Retrieve booking with the given ID
     * @param id ID of booking
     */
    public getWithID(id: string) {
        return this.item(id);
    }

    /**
     * Check for conflicts between two lists of bookings
     * @param list First list to check for conflicts
     * @param comparison Second list to check for conflicts. Defaults to the users bookings
     */
    public checkConflicts(list: IBooking[], comparison?: IBooking[]) {
        if (!comparison) {
            comparison = this.get('timeline') || [];
        }
        let conflict_count = 0;
        for (const bkn of list) {
            const bkn_start = dayjs(bkn.date);
            const bkn_end = bkn_start.add(bkn.duration, 'm');
            for (const cmp of comparison) {
                const cmp_start = dayjs(cmp.date);
                const cmp_end = cmp_start.add(cmp.duration, 'm');
                if (
                    (bkn_start.isAfter(cmp_start, 's') && bkn_start.isBefore(cmp_end, 'm')) ||
                    (bkn_end.isAfter(cmp_start, 'm') && bkn_end.isBefore(cmp_end, 's'))
                ) {
                    conflict_count++;
                    break;
                }
            }
        }
        return conflict_count;
    }

    /**
     * Generate a list of free time slots between the given bookings
     * @param list List of bookings to find slots between
     * @param min_size Minimum length of a free slot in minutes
     */
    public getFreeSlots(list: IBooking[], min_size: number = 30): IBookingSlot[] {
        if (!list) {
            return [
                {
                    start: 0,
                    end:
                        dayjs()
                            .startOf('m')
                            .valueOf() * 10
                }
            ];
        }
        const slots: IBookingSlot[] = [];
        let start = dayjs(0);
        list.sort((a, b) => a.date - b.date);
        for (const booking of list) {
            const bkn_start = dayjs(booking.date).subtract(booking.setup, 's');
            const bkn_end = dayjs(booking.date)
                .add(booking.duration, 'm')
                .add(booking.breakdown, 's');
            if (bkn_start.isAfter(start.subtract(1, 'm'), 'm')) {
                const diff = bkn_start.diff(start, 'm');
                if (diff >= min_size) {
                    slots.push({ start: start.valueOf(), end: bkn_start.valueOf() });
                }
                start = bkn_end;
            }
        }
        slots.push({
            start: start.valueOf(),
            end:
                dayjs()
                    .startOf('m')
                    .valueOf() * 10
        });
        return slots;
    }

    /**
     * Gets the next available block between the given bookings
     * @param list List of bookings
     * @param gap Minimum size for the available block. Defaults to `30` minutes
     * @param time Time to check is free. Defaults to the current time
     */
    public getNextFreeBlock(
        list: IBooking[],
        date: number = dayjs().valueOf(),
        min_size: number = 30
    ) {
        const slots = this.getFreeSlots(list, min_size);
        const today = dayjs(date);
        const time = today
            .startOf('m')
            .second(1);
        for (const block of slots) {
            const start = dayjs(block.start).startOf('m');
            const end = dayjs(block.end).startOf('m');
            if (start.isAfter(time, 's')) {
                return block;
            } else if (time.isBefore(end, 's')) {
                const duration = end.diff(time, 'm');
                if (duration >= min_size) {
                    return block;
                }
            }
        }
        return slots[slots.length - 1];
    }

    /**
     * This method will take the entire payload of bookings and will provide us with the
     * @param raw_item
     */
    public processItem(raw_item: { [name: string]: any }) {
        const user = this.parent.Users.current();
        const start = dayjs(
            raw_item.start_epoch * 1000 || raw_item.start || raw_item.Start || raw_item.date
        );
        const end = dayjs(raw_item.end_epoch * 1000 || raw_item.end || raw_item.End);
        const has_end = raw_item.end_epoch * 1000 || raw_item.end || raw_item.End;
        const duration = Math.abs(start.diff(end, 'm'));
        if (raw_item.all_day) {
            end.endOf('d');
        }
        const item: IBooking = {
            id: raw_item.id,
            icaluid: raw_item.icaluid,
            title: raw_item.title || raw_item.Subject || raw_item.subject,
            date: start.valueOf(),
            start_hours: start.hour() + start.minute() / 60,
            description: raw_item.description,
            duration: has_end ? duration || raw_item.duration : raw_item.duration,
            attendees: this.parent.Users.processList(raw_item.attendees || []),
            expected_attendees: raw_item.expected_attendees || {},
            organiser: raw_item.owner,
            is_organiser: false,
            room: { id: raw_item.room_id, name: raw_item.room_name } as IRoom,
            visitors: raw_item.visitors,
            location_name: raw_item.location_name,
            locations: raw_item.locations,
            display: {
                date: start.format('DD/MM/YYYY'),
                long_date: start.format('DD MMMM YYYY'),
                time:
                    duration < 24 * 60
                        ? `${start.format('h:mma')} - ${end.format('h:mma')}`
                        : `${start.format('DD MMM YYYY h:mma')} - ${end.format(
                              'DD MMM YYYY h:mma'
                          )}`,
                start: start.format('h:mma'),
                end: has_end
                    ? end.format('h:mma')
                    : start.add(raw_item.duration, 'm').format('h:mma'),
                duration: Utils.humaniseDuration(
                    has_end ? duration || raw_item.duration : raw_item.duration
                ),
                room: raw_item.location_name
            },
            booked_by: raw_item.booked_by,
            space_notes: raw_item.space_notes || {},
            equipment_code: raw_item.equipment_code || {},
            notes: raw_item.notes || [],
            state: raw_item.show_as,
            approval_status: raw_item.status,
            booking_type: { id: raw_item.booking_type },
            notify_users: raw_item.notify_users,
            all_day: !!raw_item.all_day || duration > 12 * 60,
            catering: processCateringData(raw_item.catering || []),
            needs_catering: raw_item.needs_catering,
            setup: raw_item.setup || 0,
            breakdown: raw_item.breakdown || 0,
            spaces: typeof raw_item.room_id === 'string' ? [raw_item.room_id] : raw_item.room_id,
            recurrence: raw_item.seriesMasterId && raw_item.recurrence ? {
                period: RECURRENCE_PERIODS.map(i => i.toLowerCase()).indexOf(raw_item.recurrence.period),
                end: dayjs(raw_item.recurrence.end * 1000).valueOf(),
                start: dayjs(raw_item.recurrence.start * 1000).valueOf(),
                series_id: raw_item.seriesMasterId
            } : null
        };
        item.notes.sort((a, b) => a.date - b.date);
        // Setup organiser for booking
        if (raw_item.organiser || raw_item.organizer) {
            const email =
                typeof raw_item.organiser === 'string'
                    ? raw_item.organiser
                    : typeof raw_item.organizer === 'string'
                    ? raw_item.organizer
                    : (raw_item.organiser || raw_item.organizer || {}).email;
            // Get the organiser's details
            item.organiser =
                this.parent.Users.get(email) || raw_item.organiser || raw_item.organizer;
            if (item.organiser && item.organiser.email) {
                item.organiser.email = item.organiser.email.toLowerCase();
            }
            // Check if organiser is the current user
            item.is_organiser =
                item.organiser &&
                user &&
                (item.organiser.id === user.id ||
                    item.organiser.email.toLowerCase() === user.email.toLowerCase());
        }
        this.processRoom(item, raw_item);
        Object.defineProperty(item, 'status', {
            get: () => {
                const now = dayjs();
                if (item._status && (item as any)._updated < now.valueOf() - 60 * 1000) {
                    item._status = null;
                }
                if (!item._status) {
                    const date = dayjs(item.date);
                    const upcoming = date.subtract(15, 'm');
                    const ending = date.add(item.duration, 'm');
                    item._status = now.isBefore(upcoming, 'm')
                        ? 'future'
                        : now.isBefore(date, 'm')
                        ? 'upcoming'
                        : now.isBefore(ending, 'm')
                        ? 'in_progress'
                        : 'past';
                    (item as any)._updated = now.valueOf();
                }
                return item._status;
            },
            set: v => (item._status = v)
        });
        // this.createVisitors(item);
        return item;
    }

    public processRoom(item: IBooking, raw_item: any) {
        Object.defineProperty(item, 'room', {
            get: () => {
                let rm = null;
                // Get room associated with the booking
                const id = raw_item.room_id;
                if (item.room_list && item.room_list.length > 0) {
                    rm = item.room_list[0];
                    item.display.room =
                        item.room_list.reduce((a, b) => (a += a ? ', ' + b.name : b.name), '') ||
                        raw_item.location_name;
                    item.display.level = rm.level ? rm.level.name : '';
                    return rm;
                }
                if (raw_item.status) {
                    raw_item.status = Object.keys(raw_item.status).reduce((a, v) => {
                        a[v.toLowerCase()] = raw_item.status[v];
                        return a;
                    }, {});
                }
                if (id instanceof Array) {
                    const rm_list: IRoom[] = [];
                    item.available = raw_item.status
                        ? id.reduce((a, v) => a && raw_item.status[v] === 'accepted', true)
                        : true;
                    for (const rm_id of id) {
                        const room = this.parent.Rooms.item(rm_id) || {
                            id: rm_id,
                            name: rm_id,
                            email: rm_id
                        };
                        if (room) {
                            rm_list.push(room as any);
                        }
                    }
                    item.room_list = rm_list;
                    if (item.room_list.length > 0) {
                        rm = item.room_list.length[0] || {};
                        const same_level =
                            item.room_list.filter(
                                i => i.level && rm.level && i.level.id === rm.level.id
                            ).length >= item.room_list.length;
                        item.display.level = same_level ? rm.level.name : 'Multiple levels';
                        item.display.room =
                            item.room_list.reduce(
                                (a, b) => (a += a ? ', ' + b.name : b.name),
                                ''
                            ) || raw_item.location_name;
                    }
                } else {
                    item.available = raw_item.status ? raw_item.status[id] === 'accepted' : true;
                    rm = this.parent.Rooms.item(id);
                    if (rm) {
                        item.display.room = rm.name || raw_item.room_name || raw_item.location_name;
                        item.display.level = rm.level ? rm.level.name : '';
                        item.room_list = [rm];
                    }
                }
                if (item.room_list) {
                    item.location_name =
                        item.room_list.map(i => i.name).join(', ') || item.location_name;
                }
                return rm;
            },
            set: v => {
                if (v) {
                    item.room_list = v instanceof Array ? v : [v];
                }
            }
        });
    }

    /**
     * Create visitor group from booking
     * @param booking Booking data
     */
    private createVisitors(booking: IBooking) {
        if (booking.visitors) {
            const visitor_list: any[] = [];
            for (const person of booking.attendees) {
                if (person.external) {
                    visitor_list.push(person);
                }
            }
            if (!visitor_list || visitor_list.length <= 0) {
                return;
            }
            const group = {
                id: booking.id,
                visitors: visitor_list,
                date: booking.date,
                duration: booking.duration,
                group_name: booking.title,
                user_id: booking.organiser ? booking.organiser.id || '' : '',
                user_email: booking.organiser ? booking.organiser.email || booking.organiser : '',
                room_id: booking.room ? booking.room.id : ''
            };
            this.parent.Visitors.updateList([this.parent.Visitors.processItem(group)], false);
        }
    }

    protected format(item) {
        const form = item || {};
        if (!form.room) {
            form.room = {};
        }
        if (form.duration > 720 && form.all_day) {
            form.date = dayjs(form.date)
                .startOf('d')
                .valueOf();
        }
        const date = dayjs(form.date).startOf('m');
        let room_id: string[] = [];
        let auto_approve = [item.state !== 'tentative'];
        if (form.id) {
            if (form.room instanceof Array) {
                form.room.forEach(room => room.book_type = 'Booking');
            } else {
                form.room.book_type = 'Booking';
            }
        }
        if (localStorage) {
            const stored_date = parseInt(localStorage.getItem('STAFF.booking.date'), 10);
            const stored_duration = parseInt(localStorage.getItem('STAFF.booking.duration'), 10);
            if (
                item.id &&
                ((stored_date && +stored_date !== item.date) ||
                    (stored_duration && +stored_duration !== item.duration))
            ) {
                console.log('Check Rules');
                form.room.forEach(rm => {
                    const bld: any =
                        this.parent.Buildings.list().find(i => i.id === rm.level.bld_id) || {};
                    const rules = rulesForSpace({
                        user: item.host,
                        space: rm,
                        time: item.date,
                        duration: item.duration || 60,
                        rules: bld.booking_rules
                    });
                    rm.book_type = rules.auto_approve ? 'Book' : 'Request';
                });
            }
            localStorage.removeItem('STAFF.booking.date');
            localStorage.removeItem('STAFF.booking.duration');
        }
        if (form.room instanceof Array) {
            auto_approve = [];
            room_id = form.room.map(rm => {
                const state = (item.approval_status ? item.approval_status[rm.email] : null) || '';
                console.log('State:', rm.email, state, rm.book_type, rm.type);
                auto_approve.push(rm.book_type !== 'Request' && state.indexOf('tentative') === -1);
                return rm.email || rm.id;
            });
        } else if (form.room && (form.room.email || form.room.id)) {
            room_id.push(form.room.email || form.room.id);
            const state =
                (item.approval_status ? item.approval_status[form.room.email] : null) || '';
            console.log('State:', form.room.email, state, form.room.book_type, form.room.type);
            auto_approve = [
                form.room.book_type !== 'Request' &&
                    form.room.type !== 'Request' &&
                    state.indexOf('tentative') === -1
            ];
        }
        console.log('Auto-approve:', auto_approve, room_id);
        form.location_name =
            form.room instanceof Array ? form.room.map(i => i.name).join(', ') : form.room.name;
        if (!form.location_name) {
            form.locations = [];
        }
        const request: any = {
            start: date.unix(),
            end: date.add(form.all_day ? 24 * 60 - 1 : form.duration, 'm').unix(),
            room_id,
            title: form.title,
            description: form.description,
            attendees: form.attendees,
            expected_attendees: form.expected_attendees || {},
            other: form.other,
            from_room: form.from_room || '',
            loan_items: form.loan_items || '',
            private: form.private || false,
            parking: form.parking || false,
            catering: (form.catering || []).filter(order =>
                (form.room instanceof Array ? form.room : [form.room]).find(
                    room => room.id === order.location_id
                )
            ),
            equipment_code: form.equipment_code,
            booking_type: (form.booking_type ? form.booking_type.id : null) || 'internal',
            notify_users: form.notify_users,
            auto_approve,
            delegate: form.delegate || false,
            location_name: form.location_name,
            locations: form.locations,
            needs_catering: form.needs_catering,
            notes: form.notes || [],
            all_day: form.all_day ? date.format('YYYY-MM-DD') : false
        };
        if (item.recurrence_rooms) {
            request.conflicts = (form.recurrence_rooms || [])
                .map(i => ({ date: dayjs(i.date).unix(), room: i.room.email || i.room.id }))
                .filter(i => i.room !== room_id[0]);
        }
        if (request.catering && item.catering_code) {
            Object.keys(request.catering).forEach(k => {
                if (request.catering[k]) {
                    request.catering[k].code = item.catering_code[k];
                }
            });
        }
        const user = this.parent.Users.current();
        const now = dayjs().startOf('s');
        if (item.equipment) {
            for (const rm_id of Object.keys(item.equipment)) {
                request.notes.push({
                    type: 'equipment',
                    space: rm_id,
                    message: item.equipment[rm_id],
                    author: user.name,
                    date: now.unix()
                });
            }
        }
        if (item.catering_notes) {
            for (const rm_id of Object.keys(item.catering_notes)) {
                request.notes.push({
                    type: 'catering',
                    space: rm_id,
                    message: item.catering_notes[rm_id],
                    author: user.name,
                    date: now.unix()
                });
            }
        }
        request.notes.sort((a, b) => a.date - b.date);
        if (request.catering && item.catering_code) {
            for (const order of request.catering) {
                order.charge_code = item.catering_code[order.location_id];
            }
        }
        if (item.description) {
            if (item.description && !request.notes.find(note => note.message === item.description)) {
                request.notes.push({
                    type: 'description',
                    message: item.description,
                    author: user.name,
                    date: now.unix()
                });
            }
        }
        request.notes = Utils.unique(request.notes, 'message');
        if (item.icaluid) {
            request.icaluid = item.icaluid;
        }
        if (item.organiser || item.host || item.booked_by) {
            request.organiser = item.organiser || item.host || item.booked_by;
        }
        if (item.notify_user) {
            request.notify_users = [item.notify_user.email];
        }
        if (item.host) {
            request.host = item.host;
            const exists = form.attendees.find(
                i => i.email.toLowerCase() === item.host.email.toLowerCase()
            );
            if (!request.notify_users) {
                request.notify_users = [item.host.email];
            }
            if (!exists) {
                request.attendees.unshift(item.host);
            }
        }
        if (item.recurrence && item.recurrence.period) {
            request.recurr = {
                period: RECURRENCE_PERIODS.map(i => i.toLowerCase())[item.recurrence.period],
                end: dayjs(item.recurrence.end).unix()
            };
        }
        return request;
    }

    public updateList(list: IBooking[]) {
        super.updateList(list);
        const timeline = this.get('timeline') || {};
        // Add bookings to timeline
        for (const item of list) {
            let date = dayjs(item.date);
            const end = date.add(item.duration, 'm');
            for (; date.isBefore(end, 'm');  date = date.add(1, 'd')) {
                const day = timeline[date.format('YYYY/MM/DD')] || [];
                for (const event of day) {
                    if (item.id && event.id === item.id) {
                        day.splice(day.indexOf(event), 1);
                        break;
                    }
                }
                day.push({
                    ...item,
                    for_date: date.valueOf(),
                    order_id: `${item.icaluid}_${date.format('YYYY-MM-DD')}`
                });
                timeline[date.format('YYYY/MM/DD')] = day;
            }
        }
        // Sort events in the timeline
        for (const key in timeline) {
            if (timeline.hasOwnProperty(key)) {
                timeline[key].sort((a, b) => a.date - b.date);
            }
        }
        this.set('timeline', timeline);
        this.parent.Visitors.checkList();
    }

    public clearList() {
        super.clearList();
        this.set('timeline', {});
    }

    public removeFromList(list: IBooking[]) {
        super.removeFromList(list);
        // Also remove booking from timeline
        const timeline = this.get('timeline') || {};
        for (const item of list) {
            const date = dayjs(item.date);
            const day = timeline[date.format('YYYY/MM/DD')] || [];
            for (const event of day) {
                if ((item.id && event.id === item.id) || item.date === event.date) {
                    day.splice(day.indexOf(event), 1);
                    break;
                }
            }
        }
        this.set('timeline', timeline);
    }

    public clear(fields?: { from?: number; to?: number }) {
        if (!fields || !fields.from) {
            super.clear(fields);
        } else {
            let start = dayjs(fields.from).startOf('d');
            const end = dayjs(fields.to || fields.from);
            // Clear period in list
            const list: IBooking[] = this.get('list') || [];
            this.set<IBooking[]>(
                'list',
                list.filter(
                    i =>
                        !(
                            dayjs(i.date).isAfter(start.subtract(1, 's'), 's') &&
                            dayjs(i.date).isBefore(end, 'm')
                        )
                )
            );
            // Clear period in timeline
            const timeline = this.get('timeline') || {};
            for (; start.isBefore(end, 'm'); start = start.add(1, 'd')) {
                const date = start.format('YYYY/MM/DD');
                if (timeline[date]) {
                    delete timeline[date];
                }
            }
            this.set('timeline', timeline);
        }
    }

    protected confirmSettings(key: string, fields: { [name: string]: any } = {}): ConfirmModalData {
        const settings = super.confirmSettings(key, fields);
        const date = dayjs(fields.date || '');
        const user = this.parent.Users.current();
        const rm = fields.room
            ? fields.room instanceof Array
                ? fields.room[0]
                : fields.room
            : null;
        const room: any = rm ? this.parent.Rooms.item(rm.id) : { name: fields.room_name };
        const bkn_type: string = rm && room ? rm.book_type || room.book_type : null;
        const lvl = room ? room.level : null;
        switch (key) {
            case 'add':
                settings.title = `${bkn_type || (!lvl ? 'Schedule' : 'Book')} ${
                    lvl ? 'space' : 'meeting'
                }`;
                if (lvl) {
                    settings.content = `${bkn_type || 'Book'} space "${room.name}"<br>`;
                } else {
                    settings.content = 'Meeting ';
                }
                settings.content += `
                    on the ${date.format('Do of MMMM, YYYY')} at ${date.format('h:mma')}<br>
                    for ${Utils.humaniseDuration(fields.duration)}
                `;
                break;
            case 'delete':
                if (fields.decline) {
                    settings.title = `Decline meeting`;
                    settings.content = `Are you sure you wish to decline attendance to meeting '${fields.name ||
                        fields.title ||
                        ''}'?`;
                    settings.icon.content = 'event_busy';
                }
                break;
            case 'update':
                settings.content = `Change booking ${room.name ? 'for "' + room.name + '"' : ''}<br>
                                    to the ${date.format('Do of MMMM, YYYY')} at ${date.format(
                    'h:mma'
                )}<br>
                                    for ${Utils.humaniseDuration(fields.duration)}`;
                break;
        }
        return settings;
    }
}
