import { MapPinComponent } from '@acaprojects/ngx-widgets';
import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { IRoom } from '../../services/data/rooms.service';
import { HashMap } from '../../shared/utilities/types.interfaces';
import { AppService } from '../../services/app.service';
import { ILevel } from '../../services/data/buildings.service';
import { IBooking } from '../../services/data/bookings.service';

export interface ViewRoomModalData {
    space: IRoom;
    booking?: IBooking;
}

@Component({
    selector: 'view-room-modal',
    templateUrl: './view-room-modal.template.html',
    styleUrls: ['./view-room-modal.styles.scss']
})
export class ViewRoomModalComponent extends BaseDirective implements OnInit {
    /** Mapping of map ids to CSS styles */
    public styles: HashMap<HashMap<string>> = {};
    /** Mapping of space statuses to CSS colours */
    public colours: HashMap<string> = {};
    /** Level for the active space */
    public level: ILevel;
    /** List of features to display extra information */
    public features: any[] = [];

    public date: string;

    /** Active space to display */
    public get space(): IRoom {
        return this._data.space;
    }

    /** Booking associated with the space */
    public get booking(): IBooking {
        return this._data.booking;
    }

    constructor(
        private _dialog: MatDialogRef<ViewRoomModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: ViewRoomModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        if (!this.date) {
            this.date = 'Today';
        }
        this.colours = this._service.Settings.get('app.colors.rooms') || {};
        this.features = [
            {
                id: `area-${this.space.map_id}-status`,
                prefix: 'pin',
                content: MapPinComponent,
                data: {
                    back: this.colours.pin || '#03A9F4',
                    text: this.space.name
                }
            }
        ];
        this.styles = { Zones: { display: 'none' } };
        this.level = this._service.Buildings.getLevel(this.space.level.id);
    }
}
