import { Component, Output, EventEmitter, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { DialogEvent, Identity } from '../../shared/utilities/types.interfaces';
import { AppService } from '../../services/app.service';
import {
    ItemPickModalComponent,
    ItemPickModalData
} from '../item-pick-modal/item-pick-model.component';

export interface RoomFilterModalData {

}

@Component({
    selector: 'room-filter-modal',
    templateUrl: './room-filter-modal.template.html',
    styleUrls: ['./room-filter-modal.styles.scss']
})
export class RoomFilterModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    public model: any = {};
    /** Initial list of levels for the dropdown */
    public default_level_list: Identity[];
    /** List of selected locations */
    public locations: Identity[];
    /** List of available levels to select */
    public level_list: Identity[];
    /** Index of the selected level */
    public active_level: number;
    /** List of available space capacities to select */
    public capacity_list: Identity[];
    /** Index of the selected capacity */
    public active_capacity: number;
    /** List of available space types */
    public room_types: Identity[];
    /** Index of the active space type */
    public active_room_type: number;
    /** List of available equipment in a space */
    public extras: Identity[];
    /** Whether all spaces should be listed */
    public show_all: boolean;

    public get location(): string {
        const locations = this.locations || [];
        if (locations.length <= 0) {
            return 'Any location';
        }
        const location_list = locations.reduce((list, item) => {
            return list + (list ? ', ' : '') + item.name;
        }, '');
        return (locations.length === 1 ? '' : `${locations.length} Locations; `) + location_list;
    }

    constructor(
        private _dialog: MatDialog,
        private _dialog_ref: MatDialogRef<RoomFilterModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: RoomFilterModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        this.default_level_list = [
            { id: -1, name: 'All Levels' },
            ...this._service.Buildings.current().levels
        ];
    }

    public selectLocations() {
        const buildings = this._service.Buildings.list() || [];
        const locations = this.locations || [];
        for (const bld of buildings) {
            (bld as any).active = locations.findIndex(loc => loc.id === bld.id) >= 0;
        }
        const ref = this._dialog.open<ItemPickModalComponent, ItemPickModalData>(
            ItemPickModalComponent,
            {
                data: {
                    name: 'Locations',
                    options: buildings && buildings.length > 0 ? [...buildings] : [],
                    selected: []
                }
            }
        );
        this.subscription(
            'pick-location',
            ref.componentInstance.event.subscribe(event => {
                if (event.reason === 'done') {
                    const new_locations = [];
                    for (const bld of ref.componentInstance.selected || []) {
                        if (bld.active) {
                            new_locations.push(bld.id);
                        }
                    }
                    this.locations = new_locations || [];
                    this.event.emit({ reason: 'action' });
                }
            })
        );
        ref.afterClosed().subscribe(() => this.unsub('pick-location'));
    }
}
