import { Component, OnDestroy, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { AppService } from '../../services/app.service';
import { BaseDirective } from '../../shared/globals/base.directive';
import { IBooking } from '../../services/data/bookings.service';
import { DialogEvent } from '../../shared/utilities/types.interfaces';
import {
    ConfirmModalComponent,
    ConfirmModalData,
    CONFIRM_METADATA
} from '../confirm-modal/confirm-modal.component';
import { TimeExtensionModalComponent } from '../time-extension/time-extension.component';
import { IRoom } from '../../services/data/rooms.service';
import {
    BookingEquipmentDetailsModalComponent,
    EquipmentDetailsModalData
} from '../../shell/booking/overlays/equipment-details/equipment-details.component';

import * as dayjs from 'dayjs';

export interface MeetingDetailsModalData {
    /** Meeting to display on the modal */
    booking: IBooking;
    /** Whether local user is able act on behalf of the host */
    as_delegate: boolean;
    /** Email address of the associated delegate */
    delegate: string;
    /** Whether booking is the first in the list */
    is_first: boolean;
    /** Whether booking is the last in the list */
    is_last: boolean;
}

@Component({
    selector: 'meeting-details-overlay',
    templateUrl: './meeting-details-overlay.template.html',
    styleUrls: ['./meeting-details-overlay.styles.scss']
})
export class MeetingDetailsOverlayComponent extends BaseDirective implements OnDestroy {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Display string of the loading state of the meeting */
    public loading: string;
    /** Whether booking is being edited */
    public edit: boolean;

    /** Active booking to display */
    public get booking(): IBooking {
        return this._data.booking;
    }

    /** Whether booking is the first in the list */
    public get is_last(): boolean {
        return this._data.is_last;
    }

    /** Whether booking is the last in the list */
    public get is_first(): boolean {
        return this._data.is_first;
    }

    /** List of spaces associated with the event */
    public get space_list(): IRoom[] {
        if (!this.booking) {
            return [];
        }
        return this.booking.room_list || (this.booking.room ? [this.booking.room] : []);
    }

    /** Whether booking is in progress */
    public get in_progress(): boolean {
        if (!this.booking) {
            return false;
        }
        const now = dayjs();
        const start = dayjs(this.booking.date);
        const end = start.add(this.booking.duration, 'm');
        return now.isAfter(start, 's') && now.isBefore(end, 'm');
    }

    /** Whether booking has ended */
    public get has_ended(): boolean {
        if (!this.booking) {
            return false;
        }
        const now = dayjs();
        const start = dayjs(this.booking.date);
        const end = start.add(this.booking.duration, 'm');
        return now.isAfter(end, 's');
    }

    /** Whether booking has a space associated with it */
    public get has_space(): boolean {
        if (!this.space_list || !this.space_list.length) {
            return false;
        }
        for (const space of this.space_list) {
            if (space) {
                return true;
            }
        }
        return false;
    }

    constructor(
        private _dialog: MatDialog,
        private _dialog_ref: MatDialogRef<MeetingDetailsOverlayComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: MeetingDetailsModalData,
        private _service: AppService
    ) {
        super();
    }

    /**
     * Show the next meeting in the list
     */
    public nextBooking() {
        this.event.emit({ reason: 'action', metadata: 'next' });
    }

    /** Show the previous meeting in the list */
    public previousBooking() {
        this.event.emit({ reason: 'action', metadata: 'previous' });
    }

    /** Open confirmation modal for deleting the meeting */
    public confirmDeleteMeeting(): void {
        const ref = this._dialog.open<ConfirmModalComponent, ConfirmModalData>(
            ConfirmModalComponent,
            {
                ...CONFIRM_METADATA,
                data: {
                    title: 'Delete meeting',
                    icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                    content: `
                        <p>Are you sure you want to delete this meeting on ${
                            this.booking.display.long_date
                        }${this.booking.recurrence ? ' from your series' : ''}?</p>
                        <p>All attendees will be notified.</p>
                    `
                }
            }
        );
        this.subscription(
            'confirm',
            ref.componentInstance.event.subscribe(event => {
                if (event.reason === 'done') {
                    this.deleteMeeting();
                    ref.close();
                    this.unsub('confirm');
                }
            })
        );
    }

    /** Open confirmation modal for deleting the series of bookings */
    public confirmDeleteSeries(): void {
        if (!this.booking.recurrence) {
            return;
        }
        const ref = this._dialog.open<ConfirmModalComponent, ConfirmModalData>(
            ConfirmModalComponent,
            {
                ...CONFIRM_METADATA,
                data: {
                    title: 'Delete series',
                    icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                    content: `
                        <p>All bookings in the series will be deleted. Are you sure you want to delete the series?</p>
                        <p>All attendees will be notified.</p>
                    `
                }
            }
        );
        this.subscription(
            'confirm',
            ref.componentInstance.event.subscribe(event => {
                if (event.reason === 'done') {
                    this.deleteSeries();
                    ref.close();
                    this.unsub('confirm');
                }
            })
        );
    }

    /** Navigate the booking with the details of the current booking set */
    public duplicateMeeting(): void {
        const room_list = this.space_list;
        const booking = {
            ...this.booking,
            id: 'ad-hoc',
            room: room_list.map(room => ({ ...room, bookings: [] }))
        };
        if (localStorage) {
            localStorage.setItem('STAFF.booking_form', JSON.stringify(booking));
        }
        this._service.navigate(['/book']);
        this._dialog_ref.close();
    }

    /** Open modal to extend the length of the meeting */
    public extendMeeting() {
        this.loading = 'Extending meeting...';
        const ref = this._dialog.open(TimeExtensionModalComponent, {
            ...CONFIRM_METADATA,
            data: { booking: this.booking }
        });
        ref.afterClosed().subscribe(() => (this.loading = null));
    }

    public editMeetingRequirements() {
        this.loading = 'Editing meeting requirements...';
        const ref = this._dialog.open<
            BookingEquipmentDetailsModalComponent,
            EquipmentDetailsModalData
        >(BookingEquipmentDetailsModalComponent, {
            maxWidth: 'calc(100vw - 2em)',
            width: '32em',
            data: {
                spaces: this.space_list,
                expected_attendees: { ...this.booking.expected_attendees },
                notes: this.booking.notes
                    .filter(note => note.type === 'equipment')
                    .reduce((map, note) => {
                        map[note.space] = note.message;
                        return map;
                    }, {}),
                cost_code: { ...this.booking.equipment_code }
            }
        });
        ref.componentInstance.event.subscribe(event => {
            if (event.reason === 'done') {
                ref.close();
                this._service.Bookings.updateItem(this.booking.id, {
                    ...this.booking,
                    room: this.space_list.map(room => ({ ...room, bookings: [] })),
                    equipment: ref.componentInstance.notes,
                    equipment_code: ref.componentInstance.cost_code,
                    expected_attendees: ref.componentInstance.expected_attendees
                }).then(
                    (booking: any) => {
                        this._data.booking = booking;
                        this._service.notifySuccess('Sucessfully updated meeting requirements');
                        this.loading = null;
                    },
                    err => {
                        this._service.notifyError(
                            `Error updating meeting requirements. Error: ${err.message || err}`
                        );
                        this.loading = null;
                        throw err;
                    }
                );
            }
        });
        ref.afterClosed().subscribe(() =>
            this.timeout('equipment', () => (this.loading = null), 2000)
        );
    }

    /**
     * Open booking form to catering section
     */
    public editMeetingCatering() {
        this.editMeeting(false);
        localStorage.setItem('STAFF.booking.date', `${this.booking.date}`);
        localStorage.setItem('STAFF.booking.duration', `${this.booking.duration}`);
        this._service.navigate(['/book', 'main', 'orders']);
        this._dialog_ref.close();
    }

    /**
     * Open booking form to edit details
     */
    public editMeeting(navigate: boolean = true) {
        const room_list = this.space_list;
        const booking: any = {
            ...this.booking,
            host: this.booking.organiser,
            room: room_list.map(room => ({
                ...room,
                bookings: [],
                raw_bookings: [],
                next: null,
                current: null
            })),
            room_list: null
        };
        booking.catering_notes = {};
        booking.catering_codes = {};
        for (const space of room_list) {
            const note = this.booking.notes.find(
                a_note => a_note.type === 'catering' && a_note.space === space.id
            );
            booking.catering_notes[space.id] = note ? note.message : '';
            const order = this.booking.catering.find(an_order => an_order.location_id === space.id);
            booking.catering_codes[space.id] = order ? order.charge_code : '';
        }
        if (localStorage) {
            localStorage.setItem('STAFF.booking_form', JSON.stringify(booking));
            localStorage.setItem('STAFF.booking.date', `${booking.date}`);
            localStorage.setItem('STAFF.booking.duration', `${booking.duration}`);
        }
        if (navigate) {
            this._service.navigate(['/book']);
            this._dialog_ref.close();
        }
    }

    /** Delete the meeting */
    private deleteMeeting() {
        this.loading = 'Deleting meeting...';
        (this._service.Bookings as any).deleteItem(this.booking.id).then(
            () => {
                this._service.notifySuccess('Successfully deleted meeting.');
                this.loading = null;
                this._dialog_ref.close();
            },
            err => {
                this.loading = null;
                this._service.notifyError(`Error deleting meeting. Error: ${err.message || err}`);
            }
        );
    }

    /** Delete series */
    private deleteSeries() {
        this.loading = 'Deleting series...';
        (this._service.Bookings as any).deleteItem(this.booking.recurrence.series_id).then(
            () => {
                this._service.notifySuccess('Successfully deleted series.');
                this.loading = null;
                this._dialog_ref.close();
            },
            err => {
                this.loading = null;
                this._service.notifyError(`Error deleting series. Error: ${err.message || err}`);
            }
        );
    }
}
