import { Component, EventEmitter, Output, OnInit } from '@angular/core';

import { BaseDirective } from '../../shared/globals/base.directive';
import { DialogEvent } from '../../shared/utilities/types.interfaces';
import { AppService } from '../../services/app.service';

export interface MapFilerModalData {}

@Component({
    selector: 'map-filter-modal',
    templateUrl: './map-filter-modal.template.html',
    styleUrls: ['./map-filter-modal.styles.scss']
})
export class MapFilterModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Whether desks statuses should be shown */
    public show_desks: boolean;
    /** Whether space statuses should be shown */
    public show_rooms: boolean;
    /** Whether team space statuses should be shown */
    public show_teams: boolean;
    /** Whether zone statuses should be shown */
    public show_zones: boolean;

    constructor(
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._service.listen('EXPLORE.show.desks', state => (this.show_desks = state));
        this._service.listen('EXPLORE.show.rooms', state => (this.show_rooms = state));
        this._service.listen('EXPLORE.show.teams', state => (this.show_teams = state));
        this._service.listen('EXPLORE.show.zones', state => (this.show_zones = state));
    }

    public updateState() {
        this._service.set('EXPLORE.show.desks', this.show_desks);
        this._service.set('EXPLORE.show.rooms', this.show_rooms);
        this._service.set('EXPLORE.show.teams', this.show_teams);
        this._service.set('EXPLORE.show.zones', this.show_zones);
        this._service.set('EXPLORE.filters', !this._service.get('EXPLORE.filters'));
    }
}
