
import { trigger, style, animate, transition } from '@angular/animations';
import { Component, Inject, Output, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Utils } from '../../shared/utility.class';
import { BaseDirective } from '../../shared/globals/base.directive';
import { Identity, DialogEvent } from '../../shared/utilities/types.interfaces';
import { AppService } from '../../services/app.service';

export interface ConferencingModalData {
    conference: ConferenceDetails;
}

export interface ConferenceDetails {
    list: Identity[];
    mac_din?: string;
    zoom?: string;
    zoom_id?: string;
    password?: string;
    alt_host?: any;
}

@Component({
    selector: 'conferencing-modal',
    templateUrl: './conferencing-modal.template.html',
    styleUrls: ['./conferencing-modal.styles.scss'],
    animations: [
        trigger('show', [
            transition(':enter', [style({ opacity: 0, height: 0 }), animate(300, style({ opacity: 1, height: '*' }))]),
            transition(':leave', [style({ opacity: 1, height: '*' }), animate(300, style({ opacity: 0, height: 0 }))]),
        ])
    ]
})
export class ConferencingModalComponent extends BaseDirective {
    /** Emitter for events on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** List of available countries to  */
    public readonly countries: Identity[] = [
        { id: 'AU', name: 'Australia' },
        { id: 'UK', name: 'United Kingdom' },
        { id: 'US', name: 'USA' },
        { id: 'US', name: 'USA' },
        { id: 'US', name: 'USA' },
        { id: 'US', name: 'USA' },
    ];
    /** List of active c */
    public list: Identity[];

    public conference: ConferenceDetails = { list: [] };
    public advanced: boolean;

    constructor(
        private _dialog: MatDialogRef<ConferencingModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: ConferencingModalData,
        private _service: AppService
    ) {
        super();
    }

    /**
     * Add item to the country list
     * @param item Item the add
     */
    public updateList(item) {
        let list = this.conference.list;
        if (!list) { list = []; }
        list.push(item);
        list = Utils.unique(list, 'id');
        this.conference.list = list;
    }

    /**
     * Remove an item from the country list
     * @param item Item to remove
     */
    public remove(item) {
        let list = this.conference.list;
        if (!list) { list = []; }
        const index = list.findIndex(i => i.id === item.id);
        if (index >= 0) {
            list.splice(index, 1);
        }
        this.conference.list = list;
    }

    /**
     * Move item the start of country list
     * @param item Item to move
     */
    public setFirst(item) {
        this.conference.list.sort((a, b) => item.id === a.id ? -1 : 0);
    }
}
