import { Component, Inject, EventEmitter, Output, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { HashMap, DialogEvent } from '../../shared/utilities/types.interfaces';
import { AppService } from '../../services/app.service';

export interface UserDetailsModalData {
    /** Whether user data is saved on the backend */
    has_backend?: boolean;
}

@Component({
    selector: 'user-details-overlay',
    templateUrl: './user-details-overlay.template.html',
    styleUrls: ['./user-details-overlay.styles.scss']
})
export class UserDetailsOverlayComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();

    public search_field: string;
    public edit: boolean;
    public user: any;
    public backend: boolean;
    public form: HashMap = {};
    /** State of the user request is being processed */
    public loading: string;

    constructor(
        private _dialog: MatDialogRef<UserDetailsOverlayComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: UserDetailsModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        if (!this.form) {
            this.form = {};
        }
        if (this.search_field) {
            if ((this.search_field || '').indexOf('@') >= 0) {
                this.form.email = this.search_field;
            } else if ((this.search_field || '').indexOf(' ') >= 0) {
                const parts = this.search_field.split(' ');
                this.form.first_name = parts[0];
                this.form.last_name = parts[1];
            } else {
                this.form.first_name = this.search_field;
            }
            this.form = { ...this.form };
        }
    }

    public add(finish: boolean = true) {
        this.loading = 'Adding new user...';
        if (!this.form.first_name && !this.form.last_name) {
            this.form.name = (this.form.email || `Guest ${Math.random() * 9999}`).split('@')[0];
        } else {
            this.form.name = (this.form.first_name || '') + ' ' + (this.form.last_name || '');
        }
        if (this._data.has_backend) {
            this._service.Users.add(
                this.form.users ? this.form.users[0] || this.form : this.form
            ).then(
                () => {
                    this._service.notifySuccess('Successfully added new user.');
                    this.event.emit({ reason: 'done' });
                    this._dialog.close();
                },
                () => (this.loading = null)
            );
        } else {
            this.event.emit({ reason: 'done' });
            this._dialog.close();
        }
    }

    public update() {
        this.loading = 'Updating user details...';
        this.form.name = (this.form.first_name || '') + ' ' + (this.form.last_name || '');
        this._service.Users.update(
            this.form.id,
            this.form.users ? this.form.users[0] || this.form : this.form
        ).then(
            () => {
                this._service.notifySuccess('Successfully updated user.');
                this.event.emit({ reason: 'done' });
                this._dialog.close();
            },
            () => (this.loading = null)
        );
    }
}
