
import { Component, EventEmitter, OnInit, Output, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { DialogEvent } from '../../shared/utilities/types.interfaces';
import { IUser } from '../../services/data/users.service';

export interface AvailabilityModalData {
    /** Host of the event */
    host: IUser;
    /** Date of the event */
    date: number;
    /** Duration of the event */
    duration: number;
    /** List of attendees for the event */
    attendees: IUser[];
}

@Component({
    selector: 'availability-overlay',
    templateUrl: './availability-overlay.template.html',
    styleUrls: ['./availability-overlay.styles.scss']
})
export class AvailabilityOverlayComponent extends BaseDirective implements OnInit {
    /** Emitter for events on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Height of the overlay */
    public height: number = 100;
    /** Host of the event */
    public host: IUser;
    /** Date of the event */
    public date: number;
    /** Duration of the event */
    public duration: number = 60;
    /** List of attendees for the event */
    public attendees: IUser[] = [];

    constructor(
        public _dialog: MatDialogRef<AvailabilityOverlayComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: AvailabilityModalData
    ) {
        super();
    }

    public ngOnInit() {
        this.host = this._data.host;
        this.date = this._data.date;
        this.duration = this._data.duration;
        this.attendees = this._data.attendees;
    }

    /**
     *
     */
    public resize() {
        this.height = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);
    }

    /**
     * Close the modal
     */
    public close() {
        this._dialog.close();
    }
}
