import { Component, OnDestroy, OnInit } from '@angular/core';

import { BaseDirective } from '../../shared/globals/base.directive';
import { AppService } from '../../services/app.service';
import { ILocation } from '../../services/data/location.service';

@Component({
    selector: 'device-list-model',
    templateUrl: './device-list-modal.template.html',
    styleUrls: ['./device-list-modal.styles.scss']
})
export class DeviceListModalComponent extends BaseDirective implements OnDestroy, OnInit {
    /** List of locatable devices */
    public list: ILocation[] = [];

    constructor(private _service: AppService) {
        super();
    }

    public ngOnInit(): void {
        this.load();
        this.interval('load', () => this.load(), 5 * 1000);
    }

    public load() {
        this._service.Location.query({}).then(
            list => this.list = list || [],
            () => null
        );
    }

    public show(location: ILocation) {

    }
}
