import { Component, Output, EventEmitter, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

import { AppService } from '../../services/app.service';
import { BaseDirective } from '../../shared/globals/base.directive';
import { DialogEvent, Identity } from '../../shared/utilities/types.interfaces';
import { IBooking } from '../../services/data/bookings.service';
import { Utils } from '../../shared/utility.class';

import * as dayjs from 'dayjs';

/** Maximum amount of time that the booking can be extended */
const MAX_EXTENSION = 60;

export interface TimeExtensionModalData {
    booking: IBooking;
}

@Component({
    selector: 'time-extension',
    templateUrl: './time-extension.template.html',
    styleUrls: ['./time-extension.styles.scss']
})
export class TimeExtensionModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Duration options that the user can select */
    public durations: Identity[] = [];
    /** Display string for the loading state of the booking */
    public loading: string;

    public get booking(): IBooking {
        return this._data.booking;
    }

    constructor(
        private _dialog: MatDialogRef<TimeExtensionModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: TimeExtensionModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        this.checkAvailability(MAX_EXTENSION);
    }

    public checkAvailability(extension: number) {
        if (extension <= 0) {
            this._service.notifyInfo('Meeting is not available to be extended.');
            this._dialog.close();
            return;
        }
        this.loading = 'Checking availability of spaces...';
        const from = dayjs(this.booking.date);
        const to = from.add(this.booking.duration + extension, 'm');
        this._service.Rooms.available({
            room_ids: this.booking.room_list.map(room => room.id).join(','),
            date: this.booking.date,
            duration: this.booking.duration + extension,
            ignore: this.booking.icaluid,
            hide_bookings: true
        }).then(
            list => {
                if (list.length >= this.booking.room_list) {
                    this.durations = [];
                    let extended = 15;
                    while (extended <= extension) {
                        this.durations.push({
                            id: this.booking.duration + extended,
                            name: ` Extend ${Utils.humaniseDuration(extended)}`
                        });
                        extended += 15;
                    }
                    this.loading = null;
                } else {
                    this.checkAvailability(extension - 15);
                }
            },
            err => {
                this._service.notifyError(`Error extending meeting. Error: ${err.message | err}`);
                this._dialog.close();
            }
        );
    }

    /**
     * Extend the meeting but the given duration
     * @param block Duration block to update the meeting to
     */
    public extendMeeting(block: Identity) {
        this.loading = 'Extending meeting...';
        this._service.Bookings.updateItem(this.booking.id, {
            ...this.booking,
            duration: block.id
        }).then(
            () => {
                this.loading = null;
                this.event.emit({ reason: 'done' });
                this._dialog.close();
            },
            err => {
                this._service.notifyError(`Error extending meeting. Error: ${err.message | err}`);
            }
        );
    }
}
