import { Injectable } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { SwUpdate } from '@angular/service-worker';
import { BehaviorSubject, Observable } from 'rxjs';

import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog } from '@angular/material/dialog';

import { HeapIoService } from '@acaprojects/ngx-heap-io';
import { ComposerService } from '@acaprojects/ngx-composer';
import { ComposerOptions } from '@acaprojects/ts-composer';

import { SettingsService } from './settings.service';
import { AnalyticsService } from './data/analytics.service';
import { Utils } from '../shared/utility.class';

import { UsersService } from './data/users.service';
import { BuildingsService } from './data/buildings.service';
import { CateringService } from './data/catering.service';
import { CommentsService } from './data/comments.service';
import { RoomsService } from './data/rooms.service';
import { BookingsService } from './data/bookings.service';
import { VisitorsService } from './data/visitors.service';
import { HotkeyService } from './hotkey.service';
import { LockersService } from './data/lockers.service';
import { ContactsService } from './data/contacts.service';
import { LocationService } from './data/location.service';

import { CateringMenuService } from './data/menu.service';
import { environment } from '../../environments/environment';
import { ComposerSettings, ApplicationIcon } from '../shared/utilities/settings.interfaces';
import { BaseClass } from '../shared/globals/base.class';
import { ConfirmModalData, ConfirmModalComponent } from '../overlays/confirm-modal/confirm-modal.component';
import { DialogEvent } from '../shared/utilities/types.interfaces';

declare global {
    interface Window {
        application: AppService;
    }
}

@Injectable({
    providedIn: 'root'
})
export class AppService extends BaseClass {
    private api_base = 'api/staff';

    private subjects: { [name: string]: BehaviorSubject<any> } = {};
    private observers: { [name: string]: Observable<any> } = {};

    private prev_route: string[] = [];
    private model: { [name: string]: any } = {};
    private timers: { [name: string]: number } = {};

    constructor(
        private _title: Title,
        private _cache: SwUpdate,
        private _router: Router,
        private _snackbar: MatSnackBar,
        private _composer: ComposerService,
        private analytics: AnalyticsService,
        private settings: SettingsService,
        private hotkeys: HotkeyService,
        private lockers: LockersService,
        private buildings: BuildingsService,
        private bookings: BookingsService,
        // private catering: CateringService,
        private comments: CommentsService,
        private rooms: RoomsService,
        private users: UsersService,
        private visitors: VisitorsService,
        private location: LocationService,
        private contacts: ContactsService,
        private heapio: HeapIoService,
        private _menu: CateringMenuService,
        private _dialog: MatDialog
    ) {
        super();
            // Set parent service on child services
        this.buildings.parent = this.bookings.parent = this.rooms.parent = this.comments.parent = this;
        this.users.parent = this.analytics.parent = this.visitors.parent = this;
        this.lockers.parent = this.contacts.parent = this.location.parent = this._menu.parent = this;
            // Create subjects
        this.subjects.system = new BehaviorSubject('');
        this.observers.system = this.subjects.system.asObservable();
        this.subjects.systems = new BehaviorSubject<any[]>([]);
        this.observers.systems = this.subjects.systems.asObservable();
            // Register modals/overlay components
        this.setupCache();
        this.init();
    }

    get endpoint() {
        const host = this.Settings.get('composer.domain');
        const protocol = this.Settings.get('composer.protocol');
        const port = ((protocol === 'https:') ? '443' : '80');
        const url = `${protocol || location.protocol}//${host || location.host}`;
        const endpoint = `${url}`;
        return endpoint;
    }

    get api_endpoint() {
        return `${this.endpoint}/${this.api_base}`;
    }

    public init(tries: number = 0) {
        if (!this.settings.setup) {
            if (tries === 5)  { this.settings.init(); }
            return setTimeout(() => this.init(), 500);
        }
        if (this.settings.get('debug')) {
            window.application = this;
            this.hotkeys.listen(['Alt', 'Shift', 'KeyK'], () => {
                const token = this._composer.auth.token;
                Utils.copyToClipboard(token);
                this.notifyInfo('Copied access token to clipboard');
            });
        }
        this.model.title = this.settings.get('app.title') || 'Angular Application';
        this.setupComposer();
        this.subscription('composer_init', this._composer.initialised.subscribe((state) => {
            if (state) {
                this.unsub('composer_init');
                let init_state = this._composer.auth.is_online;
                this.subscription('online_state', this._composer.auth.online_state.subscribe((state) => {
                    this.timeout('online_state', () => {
                        if (state !== init_state) {
                            init_state = state;
                            this.notifyInfo(
                                state
                                    ? 'Reconnected to server.'
                                    : 'Connection to server lost. Limited functionality will be be available until it reconnects.'
                            );
                        }
                    }, 1000);
                }));
                this.analytics.init();
                // Initialise data services
                this.buildings.init();
                this.bookings.init();
                this.users.init();
                this.rooms.init();
                // this.catering.init();
                this.visitors.init();
                this.lockers.init();
                this.contacts.init();
                this.initHeapIo();
            }
        }));
    }

    public ready() {
        const mock = this.settings.get('mock') ? (window as any).backend : null;
        const app_ready = this.settings.setup && this.users.current() && this.buildings.current();
        return app_ready && (!mock || mock.is_loaded);
    }

    /**
     * Initialise the composer library comms
     */
    private setupComposer(): void {
        this.log('SYSTEM', 'Setup up composer...');
        // Get application settings
        const settings: ComposerSettings = this.settings.get('composer') || {};
        const protocol = settings.protocol || location.protocol;
        const host = settings.domain || location.hostname;
        const port = settings.port || location.port;
        const url = settings.use_domain ? `${protocol}//${host}:${port}` : location.origin;
        const route = settings.route || '';
        const mock = this.settings.get('mock');
        // Generate configuration object
        const config: ComposerOptions = {
            scope: 'public',
            host: `${host}:${port}`,
            auth_uri: `${url}/auth/oauth/authorize`,
            token_uri: `${url}/auth/token`,
            redirect_uri: `${location.origin}${route}/oauth-resp.html`,
            handle_login: !settings.local_login,
            mock
        };
        this._composer.setup(config);
    }

    /**
     * Listen to changes of given property
     * @param name Name of the property
     * @param next Callback for changes to properties value
     */
    public listen(name: string, next: (data: any) => void) {
        if (this.subjects[name]) {
            return this.observers[name].subscribe(next);
        } else {
                // Create new variable to store property's value
            this.subjects[name] = new BehaviorSubject<any>(this[name] instanceof Function ? null : this[name]);
            this.observers[name] = this.subjects[name].asObservable();
                // Create raw getter and setter for property
            if (!(this[name] instanceof Function)) {
                Object.defineProperty(this, name, {
                    get: () => this.get(name),
                    set: (v: any) => this.set(name, v)
                });
            }
            return this.observers[name].subscribe(next);
        }
    }

    /**
     * Get the current value of the given property
     * @param name Name of the property
     */
    public get(name: string) {
        return this.subjects[name] ? this.subjects[name].getValue() : null;
    }

    /**
     * Set the value of the given property
     * @param name Name of the property
     * @param value New value to assign to the property
     */
    public set(name: string, value: any) {
        if (this.subjects[name]) {
            this.subjects[name].next(value);
        } else {
                // Create new variable to store property's value
            this.subjects[name] = new BehaviorSubject<any>(value);
            this.observers[name] = this.subjects[name].asObservable();
                // Create raw getter and setter for property
            if (!(this[name] instanceof Function)) {
                Object.defineProperty(this, name, {
                    get: () => this.get(name),
                    set: (v: any) => this.set(name, v)
                });
            }
        }
    }

    get Analytics() { return this.analytics; }
    get Heapio() { return this.heapio; }
    get Settings() {
        this.settings.get = (setting: string) => this.buildings.getSetting(setting) || this.settings.getSetting(setting);
        return this.settings;
    }
    get Hotkeys() { return this.hotkeys; }
    get Bindings() { return this._composer.bindings; }
        // Getters for data/API services
    get Buildings() { return this.buildings; }
    get Bookings() { return this.bookings; }
    get Catering() { return this._menu; }
    get Rooms() { return this.rooms; }
    get Users() { return this.users; }
    get Visitors() { return this.visitors; }
    get Lockers() { return this.lockers; }
    get Contacts() { return this.contacts; }
    get Location() { return this.location; }
    get Menu() { return this._menu; }

    /**
     * Set the page title
     * @param str New value to set the page title
     */
    set title(str: string) {
        if (!this.model.title) {
            this.model.title = this.settings.get('app.title') || '';
        }
        const title = `${str ? str : ''}${this.model.title ? ' | ' + this.model.title : ''}`;
        this._title.setTitle(title || this.settings.get('app.title'));
    }

    /**
     * Get the current page title
     */
    get title() {
        return this._title.getTitle();
    }

    public logout() {
        this.users.logout();
    }

    /**
     * Wrapper for Angular Router navigate method
     * @param path Path to navigate
     * @param query Query parameter to add to the route
     */
    public navigate(path: string | string[], query?: { [name: string]: any }) {
        let path_list = [];
        if (path instanceof Array) {
            path_list = path_list.concat(path);
        } else {
            path_list.push(path);
        }
        this.prev_route.push(this._router.url);
        // if (!this.systems.resources.authLoaded) {
        this._router.navigate(path_list, { queryParams: query });
        // } else {
        // this.router.navigate([path]);
        // }
    }

    /**
     * Return to the previous page
     */
    public back() {
        if (this.prev_route.length > 0) {
            this.navigate(this.prev_route.pop());
            this.prev_route.pop();
        } else {
            this.navigate('');
        }
    }

    /**
     * Log message to the console
     * @param type Message prefix
     * @param msg Message to emit
     * @param args Arguments to attach to log
     * @param stream Stream to output the message
     */
    public log(type: string, msg: string, args?: any, stream: string = 'debug') {
        this.settings.log(type, msg, args, stream);
    }

    /**
     * Open confirm modal
     * @param options Options to pass to the modal
     * @param next Callback for events on the modal
     */
    public confirm(options: ConfirmModalData, next: (event: DialogEvent) => void) {
        const ref = this._dialog.open<ConfirmModalComponent, ConfirmModalData>(ConfirmModalComponent, {
            data: options,
        });
        this.subscription('confirm', ref.componentInstance.event.subscribe(next));
        ref.afterClosed().subscribe(() => this.unsub('confirm'));
        return ref;
    }

    /**
     * Create notification popup
     * @param type CSS Class to add to the notification
     * @param message Message to display on the notificaiton
     * @param action Display text for the callback action
     * @param on_action Callback of action on the notification
     * @param icon Icon to render to the left of the notification message
     */
    public notify(
        type: string,
        message: string,
        action: string = 'OK',
        on_action?: () => void,
        icon: ApplicationIcon = {
            type: 'icon',
            class: 'material-icons',
            content: 'info'
        }
    ): void {
        const snackbar_ref = this._snackbar.open(message, action, {
            panelClass: [type],
            duration: 5000
        });
        this.subscription(
            'snackbar_close',
            snackbar_ref.afterDismissed().subscribe(() => {
                this.unsub('snackbar_close');
                this.unsub('notify');
            })
        );
        if (action) {
            on_action = on_action || (() => snackbar_ref.dismiss());
            this.subscription(
                'notify',
                snackbar_ref.onAction().subscribe(() => on_action())
            );
        }
    }

    /**
     * Create success notification popup
     * @param msg Message to display on the notificaiton
     * @param action Display text for the callback action
     * @param on_action Callback of action on the notification
     */
    public notifySuccess(msg: string, action?: string, on_action?: () => void): void {
        const icon: ApplicationIcon = {
            type: 'icon',
            class: 'material-icons',
            content: 'done'
        };
        this.notify('success', msg, action, on_action, icon);
    }

    /**
     * Create error notification popup
     * @param msg Message to display on the notificaiton
     * @param action Display text for the callback action
     * @param on_action Callback of action on the notification
     */
    public notifyError(msg: string, action?: string, on_action?: () => void): void {
        const icon: ApplicationIcon = {
            type: 'icon',
            class: 'material-icons',
            content: 'error'
        };
        this.notify('error', msg, action, on_action, icon);
    }

    /**
     * Create warning notification popup
     * @param msg Message to display on the notificaiton
     * @param action Display text for the callback action
     * @param on_action Callback of action on the notification
     */
    public notifyWarn(msg: string, action?: string, on_action?: () => void): void {
        const icon: ApplicationIcon = {
            type: 'icon',
            class: 'material-icons',
            content: 'warning'
        };
        this.notify('warn', msg, action, on_action, icon);
    }

    /**
     * Create info notification popup
     * @param msg Message to display on the notificaiton
     * @param action Display text for the callback action
     * @param on_action Callback of action on the notification
     */
    public notifyInfo(msg: string, action?: string, on_action?: () => void): void {
        this.notify('info', msg, action, on_action);
    }

    get iOS() {
        return Utils.isMobileSafari();
    }
    /**
     * Setup handler for cache change events
     */
    private setupCache() {
        if (this._cache.isEnabled) {
            this.subscription('cache_update', this._cache.available.subscribe((event) => {
                const current = `current version is ${event.current.hash}`;
                const available = `available version is ${event.available.hash}`;
                this.log('CACHE', `Update available: ${current} ${available}`);
                this.activateUpdate()
            }));
            this.subscription('cache_activated', this._cache.activated.subscribe(() => {
                this.log('CACHE', `Updates activated. Reloading...`);
                this.notifyInfo('New version of the application is available.', 'Refresh', () => location.reload(true));
            }));
            setInterval(() => {
                this.log('CACHE', `Checking for updates...`);
                this._cache.checkForUpdate();
            }, 5 * 60 * 1000);
        }
    }

    /**
     * Update the cache and reload the page
     */
    private activateUpdate() {
        if (this._cache.isEnabled) {
            this.log('CACHE', `Activating changes to the cache...`);
            this._cache.activateUpdate().then(() => {
                this.notifyInfo('New version of the application is available.', 'Refresh', () => location.reload(true));
            });
        }
    }

    /**
     * Initialise heap io for analytics
     */
    private initHeapIo() {
        if (!this.Buildings.organisation()) {
            return setTimeout(() => this.initHeapIo(), 300);
        }
        this.Heapio.load(this.Settings.get('app.heapio'));
        setTimeout(() => this.setHeapIdentity(), 1000);
    }

    /**
     * Set the identity of the current user for heap analytics
     */
    private setHeapIdentity() {
        const user = this.users.current();
        if (!user || !user.fmno) {
            return setTimeout(() => this.setHeapIdentity(), 300);
        }
        this.Heapio.identify(user.fmno);
    }

}
