
import { Component, Inject, OnInit, Output, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { FormValidators } from '../../shared/components/booking-form/form.validators';
import { HashMap, DialogEvent } from '../../shared/utilities/types.interfaces';
import { BaseDirective } from '../../shared/globals/base.directive';
import { CUSTOM_FIELD_REGISTER } from '../../shared/globals/custom-field-register';

import * as dayjs from 'dayjs';

export interface TimePeriodModalData {
    start_time?: number;
    date?: number;
    duration?: number;
    simple?: boolean;
}

@Component({
    selector: 'time-period-modal',
    templateUrl: './time-period-modal.template.html',
    styleUrls: ['./time-period-modal.styles.scss'],
})
export class TimePeriodModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();

    public form: HashMap = {};
    public fields: any[] = [];

    public get simple(): boolean {
        return !!this._data.simple;
    }

    constructor(
        private _dialog: MatDialogRef<TimePeriodModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: TimePeriodModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        this.form = {};
        this.fields = [
            {
                'key': 'date',
                'label': 'Date',
                hide: true,
                'control_type': 'custom',
                value: this._data.date,
                validators: [FormValidators.date],
                cmp: CUSTOM_FIELD_REGISTER.date
            },
            {
                'key': 'start',
                'label': 'Time',
                'refs': [ 'date' ],
                'control_type': 'custom',
                value: this._data.start_time,
                cmp: CUSTOM_FIELD_REGISTER.time
            },
            {
                'key': 'duration',
                'label': 'Duration',
                'refs': [ 'date' ],
                'control_type': 'custom',
                value: this._data.duration,
                validators: [FormValidators.duration()],
                cmp: CUSTOM_FIELD_REGISTER.duration
            }
        ];
        this.updateForm(this.form);
    }

    public updateForm(form) {
        this.form.start = form.start || form.start_time;
        const parts = form.start.split(':');
        const end = dayjs().hour(+parts[0]).minute(+parts[1]).add(form.duration, 'm');
        this.form.end = end.format('HH:mm');
    }
}
