
import { Component, OnInit, EventEmitter, Output, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { DialogEvent } from '../../shared/utilities/types.interfaces';
import { CalendarModalData } from '../calendar-modal/calendar-modal.component';
import { AppService } from '../../services/app.service';

import * as dayjs from 'dayjs';

export interface DatePeriodModalData {
    /** Active date */
    start: number;
    /** Active date */
    end: number;
    /** Number of days the user can actively book ahead */
    limit?: number;
}

@Component({
    selector: 'date-period-modal',
    templateUrl: './date-period-modal.template.html',
    styleUrls: ['./date-period-modal.styles.scss'],
})
export class DatePeriodModalComponent extends BaseDirective implements OnInit {
    /** Emitter for events on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Unix epoch in ms of the current time */
    public readonly today = dayjs().valueOf();
    /** Unix epoch in ms of the last selectable date */
    public available_end: number = dayjs()
        .add(1, 'y')
        .valueOf();
    /** Unix epoch in ms of the period start time */
    public start: number;
    /** Unix epoch in ms of the period end time */
    public end: number;

    public get start_display(): string {
        return dayjs(this.start).format('ddd DD MMMM');
    }

    public get end_display(): string {
        return dayjs(this.start).format('ddd DD MMMM YYYY');
    }

    public get duration(): string {
        const duration = Math.floor(dayjs(this.end).diff(dayjs(this.start), 'd') + 1);
        return `${duration} day${duration === 1 ? '' : 's'}`;
    }

    constructor(
        private _dialog: MatDialogRef<DatePeriodModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: DatePeriodModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        const limit = this._data.limit || this._service.Settings.get('app.booking.limit') || 365;
        if (limit && limit > 0) {
            this.end = dayjs()
                .add(limit, 'd')
                .valueOf();
        }
        this.start = this._data.start || dayjs().valueOf();
        this.end = this._data.end || dayjs(this.start).add(1, 'd').valueOf();
    }

    public updateStart() {
        this.end++;
        if (!this.end || this.end < this.start) {
            this.end = this.start;
        }
    }

    public updateEnd() {
        if (!this.end || this.end < this.start) {
            this.end = this.start;
        }
    }
}

