import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

import { IRoom } from '../../../../services/data/rooms.service';
import { IBuilding } from '../../../../services/data/buildings.service';
import { BaseDirective } from '../../../../shared/globals/base.directive';
import { DialogEvent, HashMap } from '../../../../shared/utilities/types.interfaces';
import { AppService } from '../../../../services/app.service';

export interface EquipmentDetailsModalData {
    /** List of spaces associated with the booking */
    spaces: IRoom[];
    /** Mapping of spaces to expected attendees */
    expected_attendees: HashMap<number>;
    /** Mapping of spaces to catering notes */
    notes: HashMap<string>;
    /** Mapping of spaces to catering charge codes */
    cost_code: HashMap<string>;
    /** Whether it is a recurrence booking that is created */
    recurrence?: boolean;
}

@Component({
    selector: 'equipment-details-modal',
    templateUrl: './equipment-details.component.html',
    styleUrls: ['./equipment-details.component.scss']
})
export class BookingEquipmentDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** ID of the space to show details for */
    public active_space = 'default';
    /** Mapping of spaces to form errors */
    public errors: {
        [space_id: string]: { charge_code: boolean; expected_attendees: boolean };
    } = {};
    /** Whether it is the last step */
    public finish: boolean;

    /** Selected spaces for booking */
    public get spaces(): IRoom[] {
        return this._data.spaces;
    }

    /** Whether the active space requires a charge code */
    public get requires_code(): boolean {
        return this.isCodeRequired(this.active_space);
    }

    /** Mapping of spaces to expected attendee count */
    public get expected_attendees(): HashMap<number> {
        return this._data.expected_attendees;
    }

    /** Mapping of spaces to catering notes */
    public get notes(): HashMap<string> {
        return this._data.notes;
    }

    /** Mapping of spaces to catering charge codes */
    public get cost_code(): HashMap<string> {
        return this._data.cost_code;
    }

    /** Whether it is a recurrence booking that is created */
    public get recurrence(): boolean {
        return this._data.recurrence;
    }

    /** Whether form has errors */
    public get has_errors(): boolean {
        return Object.keys(this.errors).length > 0;
    }

    constructor(
        private _dialog_ref: MatDialogRef<BookingEquipmentDetailsModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: EquipmentDetailsModalData,
        private _service: AppService
    ) {
        super();
        this._dialog_ref.disableClose = true;
    }

    public ngOnInit(): void {
        if (!this._data.expected_attendees) {
            this._data.expected_attendees = {};
        }
        if (!this._data.notes) {
            this._data.notes = {};
        }
        if (!this._data.cost_code) {
            this._data.cost_code = {};
        }
        if (this._data.spaces && this._data.spaces.length > 0) {
            this.active_space = this._data.spaces[0].id;
        }
    }

    /**
     * Check form and move on to the next step in the booking flow
     */
    public continue() {
        if (this.isValid()) {
            this.event.emit({ reason: 'done' });
        } else {
            this.updateErrors();
        }
    }

    public close() {
        this._dialog_ref.close();
    }

    /**
     * Update form errors
     */
    private updateErrors() {
        this.errors = {};
        return this.spaces.forEach(space => {
            const code_error = this.isCodeRequired(space.id) && !this._data.cost_code[space.id];
            const attendee_error = !this._data.expected_attendees[space.id];
            if (code_error || attendee_error) {
                this.errors[space.id] = {
                    charge_code: code_error,
                    expected_attendees: attendee_error
                };
            }
        });
    }

    /**
     * Whether current state of the form is valid for the selected spaces
     */
    private isValid(): boolean {
        return this.spaces.reduce((valid, space) => {
            return (
                valid &&
                (!this.isCodeRequired(space.id) || this.cost_code[space.id]) &&
                !!this.expected_attendees[space.id]
            );
        }, true);
    }

    /**
     * Whether the given space requires a charge code
     * @param space_id ID of the space to check
     */
    private isCodeRequired(space_id: string) {
        const bld = this.buildingForSpace(space_id);
        if (!bld) {
            return false;
        }
        return bld.required.equipment_code;
    }

    /**
     * Get the building for the space with the given ID
     * @param space_id ID of the space to check
     */
    private buildingForSpace(space_id: string): IBuilding {
        const space = this.spaces.find(i => i.id === space_id);
        if (!space || !space.level) {
            return null;
        }
        return this._service.Buildings.get(space.level.bld_id);
    }
}
