import { Component, OnInit, Input } from '@angular/core';

import { IRoom } from '../../../services/data/rooms.service';
import { IBooking } from '../../../services/data/bookings.service';

@Component({
    selector: 'meeting-details-space',
    templateUrl: './space.component.html',
    styleUrls: ['./space.component.scss']
})
export class MeetingDetailsSpaceComponent {
    /** Active Space to display details for */
    @Input() space: IRoom;
    /** Booking associated with the space */
    @Input() booking: IBooking;

    /** Latest equipment requirement notes for the active space */
    public get notes(): string {
        const space_notes = (this.booking.notes || []).filter(note => note.space === this.space.id);
        return space_notes.length ? space_notes[space_notes.length - 1].message : '<No requirements>';
    }

    /** Latest equipment charge code for the active space */
    public get charge_code(): string {
        return (this.booking.equipment_code || {})[this.space.id] || '<No charge code>';
    }

    /** Expected number of attendees for the active space */
    public get expected_attendees(): number {
        return (this.booking.expected_attendees || {})[this.space.id] || 0;
    }
}
