
import { Component, EventEmitter, Output, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { Identity, DialogEvent, HashMap } from '../../shared/utilities/types.interfaces';

export interface ItemPickModalData {
    /** Name of the data type being selected */
    name: string;
    /** List of available options for the data type */
    options: Identity[];
    /** List of already selected items */
    selected: Identity[];
    /** Whether multiple items can be selected */
    multiple?: boolean;
}

@Component({
    selector: 'item-pick-model',
    templateUrl: './item-pick-modal.template.html',
    styleUrls: ['./item-pick-modal.styles.scss']
})
export class ItemPickModalComponent extends BaseDirective {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Actively selected options */
    public selected: Identity[];

    public get options(): Identity[] {
        return this._data.options || [];
    }

    public get name(): string {
        return this._data.name || '';
    }

    public get multiple(): boolean {
        return !!this._data.multiple;
    }

    public get selected_map(): HashMap<boolean> {
        return (this.selected || []).reduce((map, item) => {
            map[item.id] = true;
            return map;
        }, {});
    }

    constructor(
        private _dialog: MatDialogRef<ItemPickModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: ItemPickModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        this.selected = this._data.selected || [];
    }

    public select(item?: Identity) {
        this.selected = item ? [item] : this.selected;
        this.event.emit({ reason: 'done' });
        this._dialog.close();
    }

    public toggle(item) {
        item.active = !item.active;
    }

}
