import { CateringItem } from './catering-item.class';
import { HashMap } from '../../../shared/utilities/types.interfaces';

export class CateringCategory extends CateringItem {
    /** List of items associated with the category */
    readonly items: readonly CateringItem[];
    /** Whether child items are part of a package */
    readonly package: boolean;

    public get order_anytime(): boolean {
        return (
            this._order_anytime ||
            this.items.reduce(
                (anytime, item) => anytime || item.order_anytime,
                false
            )
        );
    }

    constructor(data: HashMap) {
        super(data);
        this.items = (data.items || []).map(item =>
            item.items ? new CateringCategory(item) : new CateringItem(item)
        );
        this.package = data.package === 'true' || data.package === true;
    }

    /**
     * Convert class object into plain object
     */
    public toJSON(this: CateringItem): HashMap {
        const obj = super.toJSON();
        obj.items = obj.items.map((item: CateringItem) => item.toJSON());
        return obj;
    }
}
