import { Injectable } from "@angular/core";
import { ComposerService } from "@acaprojects/ngx-composer";

import { BaseService } from "./base.service";
import { HashMap } from "../../shared/utilities/types.interfaces";
import { CateringItem } from "./catering/catering-item.class";
import { CateringCategory } from "./catering/catering-category.class";

@Injectable({
    providedIn: "root"
})
export class CateringMenuService extends BaseService<CateringItem | CateringCategory> {
    constructor(protected _composer: ComposerService) {
        super(_composer);
        this.model.name = "catering-menu";
        this.model.route = "/menu";
    }

    /**
     * Convert user data to local format
     * @param user User data
     */
    protected processItem(item: HashMap) {
        return item.items ? new CateringCategory(item) : new CateringItem(item);
    }
}
