import { Component, EventEmitter, Output, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { HashMap, DialogEvent } from '../../../../shared/utilities/types.interfaces';
import { BaseDirective } from '../../../../shared/globals/base.directive';

import { IRoom } from '../../../../services/data/rooms.service';

import * as dayjs from 'dayjs';

export interface RecurrenceSelectData {
    date: number;
    list: IRoom[];
}

@Component({
    selector: 'recurrence-room-select-modal',
    templateUrl: './recurrence-room-select.template.html',
    styleUrls: ['./recurrence-room-select.styles.scss']
})
export class BookingRecurrenceRoomSelectModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Selected date in unix ms */
    public date: number;
    /** Date display */
    public display: string;
    /** Selected space */
    public space: IRoom;
    /** Spaces available */
    public list: IRoom[] = [];

    constructor(
        private _dialog: MatDialog,
        @Inject(MAT_DIALOG_DATA) private _data: any
    ) {
        super();
    }

    public select(item) {
        this.event.emit({ reason: 'done', metadata: item });
    }

    public ngOnInit(): void {
        this.date = this._data.date;
        this.display = dayjs(this.date).format('dddd, MMMM D, YYYY');
        this.space = this._data.space;
        this.list = this._data.list;
    }


}
