
import { Component, EventEmitter, Output, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { RECURRENCE_PERIODS } from '../../shared/utilities/formatting.utilities';
import { HashMap, DialogEvent } from '../../shared/utilities/types.interfaces';
import { BaseDirective } from '../../shared/globals/base.directive';
import { CalendarModalComponent, CalendarModalData } from '../calendar-modal/calendar-modal.component';

import * as dayjs from 'dayjs';

export enum RecurrencePeriod {
    Daily = 1,
    Weekly = 7,
    Biweekly = 14,
    Monthly = 30,
    Yearly = 365
}

export interface RecurrenceModalData {
    date: number;
    period: RecurrencePeriod;
    end: number;
}

@Component({
    selector: 'recurrence-modal',
    templateUrl: './recurrence-modal.template.html',
    styleUrls: ['./recurrence-modal.styles.scss'],
})
export class RecurrenceModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Selected date */
    public date: number;
    /** Available recurrence periods for selection */
    public periods: string[] = RECURRENCE_PERIODS;
    /** Mapping of field errors to field name */
    public error: HashMap = {};
    /** List of error messages */
    public messages: string[] = [];

    /** Selected period of recurrence */
    public recurr_period: RecurrencePeriod;
    /** Unix epoch in ms of the end time of the recurrence */
    public recurr_end: number;

    public get end_display() {
        if (!this.recurr_end) { return ''; }
        const now = dayjs(this._data.date);
        const end = dayjs(this.recurr_end);
        return now.isBefore(end)
            ? end.format('ddd, Do MMMM, YYYY')
            : now.format('ddd, Do MMMM, YYYY');
    }

    constructor(
        private _dialog: MatDialog,
        @Inject(MAT_DIALOG_DATA) private _data: RecurrenceModalData,
    ) {
        super();
    }

    public ngOnInit(): void {
        this.recurr_period = this._data.period || 0;
        this.recurr_end = this._data.end || 0;
        this.date = this._data.date || dayjs().valueOf();
    }

    public selectDate() {
        const ref = this._dialog.open<CalendarModalComponent, CalendarModalData>(CalendarModalComponent, {
            data: {
                date: this.recurr_end,
                start: dayjs(this.date).startOf('d').valueOf(),
                limit: 365
            }
        });
        this.subscription('calendar', ref.componentInstance.event.subscribe((event: DialogEvent) => {
            if (event.reason === 'done') {
                this.recurr_end = dayjs(ref.componentInstance.date).endOf('d').valueOf();
            }
        }));
        ref.afterClosed().subscribe(() => this.unsub('calendar'));
    }

    public get display_date(): string {
        if (!this.recurr_end) {
            return 'Select end date';
        }
        return dayjs(this.recurr_end).format('ddd DD MMM YYYY');
    }

    public clear() {
        this.recurr_end = null;
    }

    public checkError() {
        let error = false;
        this.messages = [];
        if (this.recurr_period && !this.recurr_end) {
            this.messages.push('End date is required');
            this.error.recurr_end = true;
            error = true;
        }
        return error;
    }

    public save() {
        if (!this.checkError()) {
            this.error = {};
            this.messages = [];
            this.event.emit({ reason: 'done' });
        }
    }
}
