import { MockHttpRequestHandler, MockHttpRequest } from '@acaprojects/ts-composer';

import { BaseMockBackend } from './base.mock';
import { Utils } from '../../utility.class';

import * as faker from 'faker';
import * as dayjs from 'dayjs';

export class MockUsersBackend extends BaseMockBackend {
    constructor(protected model) {
        super(model);
    }

    protected load() {
        this.loadVisitors();
        this.loadUsers();
        this.loadUser().then(() => {
            this.state.next(true);
            window.control.handlers.push({
                path: `/${this.model.api_route}/people/:user`,
                metadata: this.model.user_loc,
                callback: event => {
                    if (event.route_params.user) {
                        const id = event.route_params.user;
                        if (event.metadata && event.metadata[id]) {
                            return [event.metadata[id]];
                        }
                    }
                    return null;
                }
            } as MockHttpRequestHandler);
        });
    }

    private loadUser(tries: number = 0) {
        return new Promise(resolve => {
            if (tries > 10) {
                return resolve();
            }
            if (localStorage) {
                let user: any = null;
                const email = localStorage.getItem('STAFF.user_email');
                if (email && this.model.users) {
                    for (const usr of this.model.users) {
                        if (email === usr.email) {
                            user = usr;
                        }
                    }
                    if (!user) {
                        return this.loadBasicUser().then(() => resolve());
                    }
                } else {
                    setTimeout(() => this.loadUser(++tries).then(() => resolve()), 300);
                }
            }
            this.loadBasicUser().then(() => {
                resolve();
            });
        });
    }

    private loadUsers() {
        this.model.users = [];
        for (let i = 0; i < 200; i++) {
            const firstName: string = faker.name.firstName();
            const lastName: string = faker.name.lastName();
            this.model.users.push({
                department: `${faker.company.companyName()}`,
                first_name: firstName,
                last_name: lastName,
                phone: Math.floor(Math.random() * 8999 + 1000).toString(),
                email: `${firstName.toLowerCase()}.${lastName.toLowerCase()}@${this.model.domain}`,
                name: `${firstName} ${lastName}`,
                id: `user-${Math.floor(Math.random() * 899999 + 100000).toString()}`,
                avatar_url: ''
            });
        }
        this.model.contacts = [];
        for (let i = 0; i < 20; i++) {
            const firstName: string = faker.name.firstName();
            const lastName: string = faker.name.lastName();
            this.model.contacts.push({
                organisation_name: `${faker.company.companyName()}`,
                first_name: firstName,
                last_name: lastName,
                phone: Math.floor(Math.random() * 8999 + 1000).toString(),
                email: `${firstName.toLowerCase()}.${lastName.toLowerCase()}@not-${
                    this.model.domain
                }`,
                name: `${firstName} ${lastName}`,
                lockers: [
                    {
                        id: Utils.padZero(Math.floor(Math.random() * 99999), 5),
                        expiry: dayjs()
                            .add(2, 'd')
                            .unix()
                    }
                ],
                id: `user-${Math.floor(Math.random() * 899999 + 100000).toString()}`,
                avatar_url: '',
                visitor: true
            });
        }
        // Add predefined users
        for (const name of this.model.predefined_users) {
            const split = name.split(' ');
            this.model.users.push({
                department: `${faker.company.companyName()}`,
                // phone: Math.floor(Math.random() * 8999 + 1000).toString(),
                email: `${split[0].toLowerCase()}.${split[1].toLowerCase()}@${this.model.domain}`,
                name,
                first_name: split.splice(0, 1)[0],
                last_name: split.join(' '),
                id: `user-${Math.floor(Math.random() * 899999 + 100000).toString()}`,
                avatar_url: '',
                lockers: [
                    {
                        id: Utils.padZero(Math.floor(Math.random() * 99999), 5),
                        expiry: dayjs()
                            .add(2, 'd')
                            .unix()
                    }
                ],
                organisation_id: 12,
                organisation_name: 'ACA'
            });
        }
        for (const user of this.model.users) {
            this.generateLocationFor(user);
        }
        this.createStaticUsers();
        const fn = (e: MockHttpRequest) => {
            if (e.query_params.q) {
                const q = e.query_params.q.toLowerCase();
                const list = [];
                for (const u of e.metadata) {
                    if (
                        u.email.toLowerCase().indexOf(q) >= 0 ||
                        u.name.toLowerCase().indexOf(q) >= 0
                    ) {
                        list.push(u);
                        if (e.query_params.limit && list.length >= +e.query_params.limit) {
                            break;
                        }
                    }
                }
                return list;
            } else {
                return e.metadata;
            }
        };
        window.control.handlers.push({
            path: `/${this.model.api_route}/users`,
            metadata: this.model.users,
            callback: fn
        } as MockHttpRequestHandler);
        window.control.handlers.push({
            path: `/${this.model.api_route}/users`,
            metadata: {},
            method: 'POST'
        } as MockHttpRequestHandler);
        window.control.handlers.push({
            path: `/${this.model.api_route}/contacts`,
            metadata: this.model.contacts,
            callback: fn
        } as MockHttpRequestHandler);

        window.control.handlers.push({
            path: `/${this.model.api_route}/users/:email`,
            metadata: this.model.users,
            callback: e => {
                if (e.route_params.email) {
                    for (const user of e.metadata) {
                        if (user.email.toLowerCase().indexOf(e.route_params.email.toLowerCase())) {
                            user.phone = Math.floor(Math.random() * 8999 + 1000).toString();
                            return user;
                        }
                    }
                }
                return null;
            }
        } as MockHttpRequestHandler);
    }

    private createStaticUsers() {
        const bld = this.model.bld_data['zone_bld-01'];
        this.model.users.push({
            department: 'Location Testing',
            // phone: Math.floor(Math.random() * 8999 + 1000).toString(),
            email: `fixed.test@${this.model.domain}`,
            name,
            first_name: 'Fixed',
            last_name: 'Test',
            id: `user-fixed`,
            avatar_url: '',
            organisation_id: 12,
            organisation_name: 'ACA'
        });
        this.model.user_loc['user-fixed'] = {
            level: bld.levels[0].level_id,
            desk_id: `table-${bld.levels[0].number || 1}.000`,
            building: bld.id,
            at_desk: true
        };
        this.model.users.push({
            department: 'Location Testing',
            // phone: Math.floor(Math.random() * 8999 + 1000).toString(),
            email: `wireless.test@${this.model.domain}`,
            name,
            first_name: 'Wireless',
            last_name: 'Test',
            id: `user-wireless`,
            avatar_url: '',
            organisation_id: 12,
            organisation_name: 'ACA'
        });
        this.model.user_loc['user-wireless'] = {
            x: 5000,
            x_max: 10000,
            y: 3000,
            level: bld.levels[0].level_id,
            desk_id: `table-${bld.levels[0].number || 1}.001`,
            building: bld.id,
            confidence: Math.random() * 30
        };
    }

    private loadBasicUser(user?: any, tries: number = 0) {
        if (!this.model.user_promise) {
            this.model.user_promise = new Promise(resolve => {
                if (tries > 10) {
                    this.model.user_promise = null;
                    return resolve();
                }
                if (!user && this.model && this.model.users) {
                    user = this.model.users[Math.floor(Math.random() * this.model.users.length)];
                } else if (
                    !user &&
                    (!this.model || !this.model.users || this.model.users.length <= 0)
                ) {
                    return setTimeout(() => {
                        this.model.user_promise = null;
                        this.loadBasicUser(user, ++tries).then(() => resolve());
                    }, 300);
                }
                const data = {
                    id: user.StaffGUID || user.id,
                    name: user.StaffName || user.name,
                    win_id: user.StaffWindowID || user.email,
                    email: user.StaffEmailID || user.email,
                    lockers: [
                        {
                            id: Utils.padZero(Math.floor(Math.random() * 99999), 5),
                            expiry: dayjs()
                                .add(2, 'd')
                                .unix()
                        }
                    ],
                    image: `/assets/users/${user.StaffGUID || user.id}.png`
                };
                this.generateLocationFor(data, true);
                this.model.user = data;
                window.control.handlers.push({
                    path: '/control/api/users/current',
                    metadata: data
                });
                resolve();
            });
        }
        return this.model.user_promise;
    }

    public loadVisitors() {
        this.model.visitors = [];
        const count = Math.floor(Math.random() * 10 + 2);
        const time = dayjs()
            .startOf('m')
            .add(10, 'm');
        for (let i = 0; i < count; i++) {
            const peeps = Math.floor(Math.random() * 3 + 1);
            const people = [];
            for (let k = 0; k < peeps; k++) {
                const name = faker.name.firstName() + ' ' + faker.name.lastName();
                people.push({
                    name,
                    organisation: {
                        id: 12,
                        name: 'ACA'
                    },
                    email: `${name
                        .split(' ')
                        .join('.')
                        .toLowerCase()}@${this.model.domain}`
                });
            }
            const visitor = {
                id: `group-${Math.floor(Math.random() * 89999 + 10000)}`,
                name: `Group ${'ABCDEFGHIJKLMNOPQRSTUVWXYZ'[i]}`,
                group: peeps > 1,
                visitors: people,
                date: time.valueOf()
            };
            this.model.visitors.push(visitor);
            time.add(Math.floor(Math.random() * 5) * 15 + 15, 'm');
        }
        window.control.handlers.push({
            path: `/${this.model.api_route}/visitors`,
            metadata: this.model.visitors
        });
    }

    private generateLocationFor(user: any, force: boolean = false) {
        if (!this.model.user_loc) {
            this.model.user_loc = {};
        }
        const id = user.id || user.StaffGUID || user.email;
        if (!(window as any).mock) {
            (window as any).mock = {};
        }
        if (!(window as any).mock.located) {
            (window as any).mock.located = { wireless: [], fixed: [] };
        }
        if (user && id) {
            if (Math.floor(Math.random() * 289346923) % 5 === 0 || force) {
                const fixed = Math.floor(Math.random() * 289346923) % 4 === 0;
                const loc: any = {};
                const bld_list = Object.keys(this.model.bld_data);
                const bld_id = bld_list[Math.floor(Math.random() * bld_list.length)];
                loc.building = bld_id;
                if (fixed) {
                    loc.at_desk = true;
                    if (this.model.desk_id_pool) {
                        const desk = this.model.desk_id_pool[
                            Math.floor(Math.random() * this.model.desk_id_pool.length)
                        ];
                        loc.level = desk.level;
                        loc.desk_id = desk.id;
                    }
                    (window as any).mock.located.fixed.push(user.name);
                } else {
                    loc.x = Math.floor(Math.random() * 6000 + 2000);
                    loc.x_max = 10000;
                    loc.y = Math.floor(Math.random() * 4000 + 3000);
                    loc.y_max = 10000;
                    const level = this.model.bld_data[bld_id].levels[
                        Math.floor(Math.random() * this.model.bld_data[bld_id].levels.length)
                    ];
                    loc.level = level.level_id;
                    loc.confidence = Math.random() * 30;
                    (window as any).mock.located.wireless.push(user.name);
                }
                this.model.user_loc[id] = loc;
                user.location = loc;
            }
        }
    }
}
