import { Component, Output, EventEmitter, Inject, OnInit } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { CateringOrder } from '../../../../services/data/catering/catering-order.class';
import { AppService } from '../../../../services/app.service';
import { IUser } from '../../../../services/data/users.service';
import { IRoom } from '../../../../services/data/rooms.service';
import { BaseDirective } from '../../../../shared/globals/base.directive';
import { Utils } from '../../../../shared/utility.class';
import { DialogEvent, HashMap } from '../../../../shared/utilities/types.interfaces';
import { IBooking } from '../../../../services/data/bookings.service';
import { RECURRENCE_PERIODS } from '../../../../shared/utilities/formatting.utilities';

import * as dayjs from 'dayjs';

export interface BookingDetailsModalData extends IBooking {
    time_group?: any;
    period_group?: any;
    conference?: any;
    equipment: HashMap<string>;
    catering_code: HashMap<string>;
    catering_notes: HashMap<string>;
    host?: IUser;
    check?: boolean;
}

@Component({
    selector: 'booking-details-modal',
    templateUrl: './booking-details.component.html',
    styleUrls: ['./booking-details.component.scss'],
    animations: [
        trigger('show', [
            state('show', style({ opacity: 1, height: '*' })),
            state('hide', style({ opacity: 0, height: 0 })),
            transition('show <=> hide', animate('200ms'))
        ])
    ]
})
export class BookingDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Whether the booking request is being processed */
    public loading: boolean;
    /** Whether the room availability is being checked */
    public check: boolean;
    /** Whether the booking request succeeded */
    public success: boolean;
    /** Whether to show the list of spaces */
    public show_spaces: boolean;
    /** Whether to show the catering details */
    public show_catering: boolean;
    /** Whether to show the list of attendees */
    public show_attendees: boolean;
    /** Whether to show notes */
    public show_notes: boolean;

    constructor(
        private _dialog: MatDialogRef<BookingDetailsModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: BookingDetailsModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        if (this._data.time_group) {
            this._data = { ...this._data, ...this._data.time_group };
        }
        if (this._data.period_group) {
            this._data = { ...this._data, ...this._data.period_group };
        }
    }

    /** Whether the booking needs to be request
     * Check that the booking has a room that isn't auto-approved and booking status is tentative
     */
    public get has_requested(): boolean {
        const spaces = this.recurrence_rooms ? this.recurrence_rooms.map(i => i.room) : this.spaces;
        return spaces.reduce((a, v) => {
            const status = this._data.approval_status
                ? this._data.approval_status[v.email] || ''
                : '';
            return a || v.book_type === 'Request' || status.indexOf('tentative') !== -1;
        }, false);
    }

    /** Whether availability has to be checked */
    public get check_availability(): boolean {
        return this._data.check;
    }

    /** Host of the booking */
    public get host(): IUser {
        return (
            this._data.booked_by ||
            this._data.organiser ||
            this._data.host ||
            this._service.Users.current()
        );
    }

    /** Display date for the given booking */
    public get date(): string {
        const day = dayjs(this._data.date);
        return day.format('DD MMM YYYY');
    }

    /** Duration of the booking */
    public get duration(): number {
        return this._data.duration;
    }

    /** Whether booking is all day */
    public get all_day(): boolean {
        return !!this._data.all_day;
    }

    /** Display period for the given booking */
    public get period(): string {
        const day = dayjs(this._data.date);
        const end = day.add(this._data.duration || 60, 'm');
        return `${day.format('h:mm A')} - ${end.format('h:mm A')}`;
    }

    /** List of attendees for the booking */
    public get attendees(): IUser[] {
        return this._data.attendees || [];
    }

    /** Recurrence Rooms for each date */
    public get recurrence_rooms(): any {
        if (!this._data.recurrence) {
            return null;
        }
        return this._data.recurrence_rooms;
    }

    /** Display recurrence period */
    public get recurrence(): string {
        if (!this._data.recurrence) {
            return '';
        }
        const { end, period } = this._data.recurrence;
        const pattern = RECURRENCE_PERIODS[period];
        const date = dayjs(end).format('DD MMM YYYY');
        return `${pattern} until ${date}`;
    }

    /** Display string for the names of the attendees */
    public get attendee_list(): string {
        return this.attendees.map(i => i.name).join(', ');
    }

    /** Conferencing details of the booking */
    public get conferencing(): string {
        return this._data.conference || '';
    }

    /** List of the selected rooms for the booking */
    public get spaces(): IRoom[] {
        if (!this._data.id && this._data.recurrence) {
            return Utils.unique(
                this.recurrence_rooms.map(i => i.room),
                'id'
            );
        } else {
            return this._data.room instanceof Array
                ? this._data.room
                : this._data.room
                ? [this._data.room]
                : [];
        }
    }

    /** Display string for the names of the selected rooms */
    public get space_list(): string {
        return this.spaces.map(i => i.name).join(', ');
    }

    /** List of catering orders for booking */
    public get catering(): CateringOrder[] {
        if (!this._data.catering) {
            return [];
        }
        return this._data.catering.map(order => new CateringOrder(order));
    }

    /** Total number of catering items in booking */
    public get item_count(): number {
        return this.catering.reduce((count, order) => count + order.item_count, 0);
    }

    /** Private notes for spaces and equipment */
    public get notes(): string[] {
        const notes = this._data.equipment || {};
        const note_list = Object.keys(notes).reduce((a, v) => {
            a.push(notes[v]);
            return a;
        }, []);
        return note_list;
    }

    /** Private notes for catering */
    public get catering_notes(): string {
        return `${this._data.catering_notes}`;
    }

    /** Cost codes for equipment and catering */
    public get cost_codes(): string {
        const equip_codes = this._data.equipment_code || {};
        const eq_code_list = Object.keys(equip_codes).reduce((a, v) => {
            a.push(equip_codes[v]);
            return a;
        }, []);
        const eq_codes = eq_code_list.join(', ');
        const catering_codes = this._data.catering_code || {};
        const code_list = Object.keys(catering_codes)
            .filter(i => this.spaces.find(j => j.id === i))
            .reduce((a, v) => {
                a.push(catering_codes[v]);
                return a;
            }, []);
        const codes = code_list.join(', ');
        return `${eq_codes || 'none'} | ${codes || 'none'}`;
    }

    /**
     * Perform pre-booking checks and send booking request if the pass
     */
    public makeBooking() {
        const room_ids = this.spaces.map(i => i.id).join(',');
        const date = this.all_day
            ? dayjs(this._data.date)
                  .startOf('d')
                  .valueOf()
            : this._data.date;
        const duration = this.all_day ? 24 * 60 : this._data.duration || 60;
        const now = this.all_day ? dayjs().startOf('d') : dayjs();
        const time = dayjs(date);
        if (time.isBefore(now, 'm') && now.diff(time, 'm') > 5) {
            this._service.notifyError('Booking date and time need to be in future');
            return;
        }
        this.check = true;
        this.loading = true;
        if (room_ids && this.check_availability) {
            const setup = this.item_count > 0 || this.notes.length ? 15 * 60 : 0;
            const breakdown = this.item_count > 0 || this.notes.length ? 15 * 60 : 0;
            const ignore = this._data.icaluid;
            this._service.Rooms.available({
                room_ids,
                date,
                duration,
                setup,
                breakdown,
                ignore
            }).then(
                list => {
                    this.check = false;
                    this.loading = false;
                    if (list.length === this.spaces.length) {
                        this.sendBookingRequest();
                    } else {
                        if (setup || breakdown) {
                            this._service.notifyError(
                                `Selected spaces are not free at the given time(${this.date} ${this.period}).
                             Meetings with additional requirements or catering require a minimum buffer of 15 minutes outside the selected period.`
                            );
                        } else {
                            this._service.notifyError(
                                `Selected spaces are not free at the given time(${this.date} ${this.period})`
                            );
                        }
                    }
                },
                () => {
                    this.loading = false;
                    if (setup || breakdown) {
                        this._service.notifyError(
                            `Selected spaces are not free at the given time(${this.date} ${this.period}).
                         Meetings with additional requirements or catering require a minimum buffer of 15 minutes outside the selected period.`
                        );
                    } else {
                        this._service.notifyError(
                            `Selected spaces are not free at the given time(${this.date} ${this.period})`
                        );
                    }
                }
            );
        } else {
            this.check = false;
            this.loading = false;
            this.sendBookingRequest();
        }
    }

    /**
     * Send booking request to the server
     */
    public sendBookingRequest() {
        this.loading = true;
        const request =
            this._data.id && this._data.id !== 'map-booking' && this._data.id !== 'ad-hoc'
                ? this._service.Bookings.updateItem(this._data.id, this._data)
                : this._service.Bookings.add(this._data);
        request.then(
            () => {
                this.loading = false;
                this.success = true;
                this.event.emit({ reason: 'action', metadata: 'new' });
            },
            e => {
                this.loading = false;
                this._service.notifyError(`Booking Error: ${e}`);
            }
        );
    }

    public finish() {
        this.event.emit({ reason: 'done' });
        this._service.navigate(['/schedule']);
        this._dialog.close();
    }
}
