
import { Component, Inject, OnInit, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

import { IRoom } from '../../../../services/data/rooms.service';
import { BaseDirective } from '../../../../shared/globals/base.directive';
import { HashMap, DialogEvent } from '../../../../shared/utilities/types.interfaces';

export interface CateringDetailsModalData {
    /** List of spaces in the booking */
    spaces: IRoom[];
    /** Mapping of spaces to catering notes */
    notes: HashMap<string>;
    /** Mapping of spaces to catering charge codes */
    cost_code: HashMap<string>;
}

@Component({
    selector: 'catering-details-modal',
    templateUrl: './catering-details.component.html',
    styleUrls: ['./catering-details.component.scss']
})
export class BookingCateringDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** ID of the space to show details for */
    public active_space: string = 'default';
    /** Whether booking is at the end of the flow */
    public finish: boolean;

    /** Selected spaces for booking */
    public get spaces(): IRoom[] {
        return this._data.spaces;
    }

    /** Mapping of spaces to catering notes */
    public get notes(): HashMap<string> {
        return this._data.notes;
    }

    /** Mapping of spaces to catering charge codes */
    public get cost_code(): HashMap<string> {
        return this._data.cost_code;
    }

    constructor(
        private _dialog_ref: MatDialogRef<BookingCateringDetailsModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: CateringDetailsModalData
    ) {
        super();
        this._dialog_ref.disableClose = true;
    }


    public ngOnInit(): void {
        if (!this._data.notes) {
            this._data.notes = {};
        }
        if (!this._data.cost_code) {
            this._data.cost_code = {};
        }
        if (this.spaces && this.spaces.length > 0) {
            this.active_space = this.spaces[0].id;
        }
    }

    public close() {
        this._dialog_ref.close();
    }
}

