import { Component, OnInit, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';

import { IRoom } from '../../services/data/rooms.service';
import { AppService } from '../../services/app.service';
import { Utils } from '../../shared/utility.class';
import { FormFormatters } from '../../shared/components/booking-form/form.formatters';
import { CUSTOM_FIELD_REGISTER } from '../../shared/globals/custom-field-register';
import { IBooking } from '../../services/data/bookings.service';
import { IUser } from '../../services/data/users.service';
import { DialogEvent } from '../../shared/utilities/types.interfaces';
import { BaseDirective } from '../../shared/globals/base.directive';
import { BookingEquipmentDetailsModalComponent } from '../../shell/booking/overlays/equipment-details/equipment-details.component';

import * as dayjs from 'dayjs';

export interface ExploreBookingModalData {
    space: IRoom;
    date: number;
}

@Component({
    selector: 'explore-booking-modal',
    templateUrl: './explore-booking-modal.component.html',
    styleUrls: ['./explore-booking-modal.component.scss']
})
export class ExploreBookingModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Selected room */
    public space: IRoom;
    /** Selected time */
    public date: number;
    /** Form fields */
    public form_fields: ADynamicFormField[] = [];
    /** Whether select time is current */
    public current: boolean;
    /** State of the modal */
    public state: 'confirm' | 'form' | 'processing' | 'status' = 'confirm';
    /** Maximum available time for the booking */
    public max_duration = 30;
    /** Display value for the end of the current free block */
    public end_display: string;
    /** Display value for the start time of the current booking */
    public time_display: string;
    /** Display value for the maximum booking duration */
    public duration_display: string;
    /** Form values */
    public form: { [name: string]: any };
    /** Toggle validation on booking form */
    public validate: boolean;
    /** Whether form values are valid */
    public valid: boolean;
    /** Whether booking date is today */
    public is_today: boolean = true;

    constructor(
        public _dialog: MatDialog,
        public _dialog_ref: MatDialogRef<ExploreBookingModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: ExploreBookingModalData,
        public _service: AppService
    ) {
        super();
    }

    /**
     * Method called by the overlay service when modal is created
     */
    public ngOnInit(): void {
        if (this._data.space) {
            this.space = this._data.space;
        }
        if (this._data.date) {
            this.date = this._data.date;
        }
        if (!this.space) {
            // No room is selected
            this._dialog_ref.close();
            return;
        } else if (!this.date) {
            let day = dayjs().startOf('m');
            day = day.minute(Math.ceil(day.minute() / 5) * 5);
            this.date = day.valueOf();
        }
        let date = dayjs(this.date);
        const free = this._service.Bookings.getNextFreeBlock(this.space.bookings, this.date, 1) || {
            start: date.startOf('d').valueOf(),
            end: date
                .endOf('d')
                .add(1, 'd')
                .valueOf()
        };
        let start = dayjs(free.start);
        if (date.isBefore(start)) {
            date = start;
            this.date = free.start;
        } else {
            start = date;
        }
        const end = dayjs(free.end);
        let now = dayjs();
        this.is_today = date.isSame(now, 'd');
        now = now.minute(Math.ceil(now.minute() / 5) * 5);
        this.current = date.isBefore(now.add(1, 'm'), 'm');
        this.max_duration = Math.abs(date.diff(end, 'm'));
        this.time_display = date.format('h:mm a');
        if (end.isSame(start, 'd')) {
            this.end_display = end.format('h:mm a') === '11:59 pm' ? '' : end.format('h:mm a');
        }
        this.duration_display = Utils.humaniseDuration(this.max_duration);
        this.createForm();
    }

    public createForm() {
        const fields = [
            {
                key: 'room',
                label: 'Selected Space',
                value: this.space,
                type: 'action',
                readonly: true,
                format: FormFormatters.space
            },
            {
                key: 'equipment',
                label: 'Required Equipment',
                value: {},
                type: 'custom',
                hide: true
            },
            {
                key: 'equipment_code',
                label: 'Equipment Code',
                value: {},
                type: 'custom',
                hide: true
            },
            {
                key: 'expected_attendees',
                label: 'Expected Attendees',
                value: {},
                type: 'custom',
                hide: true
            },
            {
                key: 'title',
                label: 'Meeting Subject',
                type: 'input',
                value: 'Ad-hoc Booking',
                required: true
            },
            {
                key: 'host',
                label: 'Host',
                type: 'custom',
                content: CUSTOM_FIELD_REGISTER.host,
                value: this._service.Users.current(),
                metadata: { options: this.getHostOptions() },
                required: true
            },
            {
                key: 'date',
                label: 'Date',
                hide: true,
                type: 'custom',
                content: CUSTOM_FIELD_REGISTER.date,
                value: this.date,
                required: true
            },
            {
                key: 'start',
                label: 'Time',
                type: 'custom',
                content: CUSTOM_FIELD_REGISTER.time,
                required: true
            },
            {
                key: 'duration',
                label: 'Duration',
                type: 'custom',
                content: CUSTOM_FIELD_REGISTER.duration,
                value: Math.min(60, this.max_duration),
                metadata: { max_duration: Math.min(720, this.max_duration) },
                required: true
            }
        ];
        this.form_fields = fields.map(i => new ADynamicFormField(i as any));
        this.form_fields.forEach(i => {
            this.subscription(
                i.key,
                i.control.valueChanges.subscribe(i => {
                    this.form = this.formToBooking();
                })
            );
        });
        this.form = this.form_fields.reduce((a, i) => {
            a[i.key] = i.getValue();
            return a;
        }, {});
        this.getHostOptions();
    }

    /**
     * Callback for user confirmation on room details
     */
    public confirm() {
        if (this.current) {
            this.state = 'form';
        } else {
            this.form = {
                id: 'map-booking',
                room: [
                    {
                        id: this.space.id,
                        name: this.space.name,
                        email: this.space.email,
                        book_type: this.space.book_type,
                        level: this.space.level
                    }
                ],
                date: this.date,
                duration: Math.min(60, this.max_duration)
            };
            this.event.emit({ reason: 'done', metadata: 'new' });
        }
    }

    /**
     * Callback to perform booking with set details
     */
    public book() {
        this.state = 'processing';
        this.addEquipment().then(
            () => {
                this.valid = this.form_fields.reduce((a, v) => a && v.isValid(), true);
                this.form = this.formToBooking();
                this.timeout('book', () => {
                    if (this.valid && this.form) {
                        this.form.attendees = [];
                        this._service.Rooms.isAvailable(
                            this.space.id,
                            this.form.date,
                            this.form.duration
                        ).then(
                            _ => {
                                this._service.Bookings.add(this.form).then(
                                    _ => {
                                        this.state = 'status';
                                        this.timeout(
                                            'success',
                                            () => this._dialog_ref.close(),
                                            3000
                                        );
                                    },
                                    e => {
                                        this._service.notifyError('Error completing booking.');
                                        this.state = 'form';
                                    }
                                );
                            },
                            _ => {
                                this._service.notifyError(
                                    `"${this.space.name}" is not available at ${dayjs(
                                        this.form.date
                                    ).format('h:mm a')} for ${Utils.humaniseDuration(
                                        this.form.duration
                                    )}`
                                );
                                this.state = 'form';
                            }
                        );
                    } else {
                        this.state = 'form';
                    }
                });
            },
            _ => (this.state = 'form')
        );
    }

    /**
     * Show modal to user for adding equipment details to a booking
     */
    private addEquipment() {
        return new Promise((resolve, reject) => {
            const empty = { control: { value: '' }, setValue: () => null };
            const notes = this.form_fields.find(i => i.key === 'equipment') || empty;
            const expected_attendees = this.form_fields.find(i => i.key === 'expected_attendees');
            const cost_code = this.form_fields.find(i => i.key === 'equipment_code');
            const ref = this._dialog.open(BookingEquipmentDetailsModalComponent, {
                maxWidth: 'calc(100vw - 2em)',
                width: '32em',
                data: {
                    spaces: [this.space],
                    expected_attendees: (expected_attendees || empty).control.value || {},
                    notes: (notes || empty).control.value,
                    cost_code: (cost_code || empty).control.value
                }
            });
            this.subscription('event', ref.componentInstance.event.subscribe(event => {
                if (event.reason === 'done') {
                    if (notes) {
                        notes.setValue(ref.componentInstance.notes);
                    }
                    if (cost_code) {
                        cost_code.setValue(ref.componentInstance.cost_code);
                    }
                    resolve();
                } else {
                    reject();
                }
                this.unsub('event');
                ref.close();
            }));
        });
    }

    /**
     * Convert form field data to a Booking object
     */
    private formToBooking(): IBooking {
        return this.form_fields.reduce((v, i) => {
            if (i.children && i.children.length) {
                i.children.forEach(j => (v[j.key] = j.control.value));
            } else {
                v[i.key] = i.control.value;
            }
            return v;
        }, {}) as IBooking;
    }

    /**
     * Get list of users available to select as the meeting host
     */
    private getHostOptions(): IUser[] {
        const user = this._service.Users.current();
        const list: IUser[] = [user];
        if (user.delegates) {
            for (const email of user.delegates) {
                const delegate = this._service.Users.item(email);
                if (delegate) {
                    list.push(delegate);
                } else {
                    this.timeout(`load_user|${email}`, () =>
                        this._service.Users.show(email, { update: true }).then(() => {
                            this.timeout('update_host_options', () => {
                                const host = this.form_fields.find(i => i.key === 'host');
                                if (host) {
                                    host.metadata = {
                                        service: this._service.Bookings,
                                        options: this.getHostOptions()
                                    };
                                    host.setValue(host.getValue());
                                }
                            });
                        })
                    );
                }
            }
        }
        return list;
    }
}
