import { trigger, state, style, animate, transition } from '@angular/animations';
import { Component, EventEmitter, Inject, Output, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Utils } from '../../shared/utility.class';
import { AppService } from '../../services/app.service';
import { BaseDirective } from '../../shared/globals/base.directive';
import { DialogEvent, Identity, HashMap } from '../../shared/utilities/types.interfaces';
import { IUser } from '../../services/data/users.service';

export interface GuestSettingsModalData {
    host: IUser;
    guest_settings?: HashMap;
}

@Component({
    selector: 'guest-settings-modal',
    templateUrl: './guest-settings-modal.component.html',
    styleUrls: ['./guest-settings-modal.component.scss'],
    animations: [
        trigger('show', [
            transition(':enter', [
                style({ opacity: 0, height: 0 }),
                animate(300, style({ opacity: 1, height: '*' }))
            ]),
            transition(':leave', [
                style({ opacity: 1, height: '*' }),
                animate(300, style({ opacity: 0, height: 0 }))
            ])
        ])
    ]
})
export class GuestSettingsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Whether to show autocomplete */
    public show: boolean;
    /** List of users matching the search requirements */
    public result: IUser[] = [];
    /** List of filtered users */
    public filtered: IUser[] = [];
    /** Details for the active greeter */
    public greeter: Identity;
    /** Available greeter options */
    public greeter_options: Identity[];

    /** Available settings for greeting guests */
    public get guest_settings(): HashMap {
        return this._data.guest_settings || {};
    }

    constructor(
        private _dialog: MatDialogRef<GuestSettingsModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: GuestSettingsModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        const bld = this._service.Buildings.current();
        const concierge = {
            name: `${bld.name} Concierge`,
            email: bld.reception_email || 'ln-reception@mckinsey.com'
        };
        const user = this._service.Users.current();
        const host = this._data.host;
        this.greeter_options = [
            { id: 'host', name: `Host of the meeting (${host.name})`, user: host },
            { id: 'concierge', name: 'Concierge at front desk', user: concierge },
            { id: 'other', name: 'Someone else' }
        ];
        const id = user.email
            ? user.email === host.email
                ? 'host'
                : user.email === concierge.email
                ? 'concierge'
                : 'other'
            : 'host';
        if (id === 'other') {
            this.greeter_options[2].user = { ...user };
        }
        this.updateGreeter(id);
    }

    public updateGreeter(id: string) {
        this.greeter = this.greeter_options.find(el => el.id === id);
    }

    public search(filter: string) {
        this.timeout('search', () => {
            if (filter && filter.length >= 3) {
                if (!this.result) {
                    this._service.Users.query({ q: filter }).then(users => {
                        this.result = users;
                        this.filterResult(filter);
                        this.show = true;
                    });
                } else {
                    this.filterResult(filter);
                    this.show = true;
                }
            } else {
                this.result = null;
            }
        });
    }

    public filterResult(filter: string) {
        const group = [...this.result, this._service.Contacts.list()];
        this.filtered = Utils.filter(filter, group, ['name', 'email']);
        this.filtered.forEach((user: any) => {
            if (user.match_name) {
                user.match_name = user.match_name.replace(
                    /\`[a-zA-Z0-9\@\.\_]*\`/g,
                    '<span class="highlight">$&</span>'
                );
                user.match_name = user.match_name.replace(/\`/g, '');
            }
            if (user.match_email) {
                user.match_email = user.match_email.replace(
                    /\`[a-zA-Z0-9\@\.\_]*\`/g,
                    '<span class="highlight">$&</span>'
                );
                user.match_email = user.match_email.replace(/\`/g, '');
            }
        });
    }

    public setGreeter(item) {
        this.greeter.user = item;
        this.search = null;
        this.result = null;
    }

    public removeGreeter() {
        this.greeter.user = null;
    }

    public hideTooltip() {
        this.timeout('hide_tooltip', () => (this.show = false));
    }
}
