import { Component, Inject, OnInit, Output, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from '../../shared/globals/base.directive';
import { AppService } from '../../services/app.service';
import { DialogEvent } from '../../shared/utilities/types.interfaces';

import * as dayjs from 'dayjs';

export interface CalendarModalData {
    /** Active date */
    date: number;
    /** Number of days the user can actively book ahead */
    limit?: number;
    /** Start date */
    start?: number;
}

@Component({
    selector: 'calendar-modal',
    templateUrl: './calendar-modal.template.html',
    styleUrls: ['./calendar-modal.styles.scss']
})
export class CalendarModalComponent extends BaseDirective implements OnInit {
    /** Emitter for events on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Unix epoch in ms of the current time */
    public readonly today = dayjs().valueOf();
    /** Currently selected date */
    public date: number;
    /** Unix epoch in ms of the first selectable date */
    public start: number = this.today;
    /** Unix epoch in ms of the last selectable date */
    public end: number = dayjs()
        .add(1, 'y')
        .valueOf();

    constructor(
        private _dialog: MatDialogRef<CalendarModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: CalendarModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        const limit = this._data.limit || this._service.Settings.get('app.booking.limit') || 365;
        const from  = this._data.start;
        if (from) {
            this.start = dayjs(from).startOf('d').valueOf();
        }
        if (limit && limit > 0) {
            this.end = dayjs()
                .add(limit, 'd')
                .valueOf();
        }
        this.date = this._data.date || dayjs().valueOf();
    }

    public onSubmit(): void {
        this.event.emit({reason: 'done'});
        this._dialog.close();
    }
}
