import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { IBooking } from '../../services/data/bookings.service';

import * as dayjs from 'dayjs';
import { BaseDirective } from '../../shared/globals/base.directive';

export interface ConfirmRequestModalData {
    booking: IBooking;
}

@Component({
    selector: 'confirm-request-modal',
    templateUrl: './confirm-request-modal.component.html',
    styleUrls: ['./confirm-request-modal.component.scss']
})
export class ConfirmRequestModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<any>();
    /** Booking to confirm request */
    public booking: IBooking;

    constructor(
        private _dialog: MatDialogRef<ConfirmRequestModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: ConfirmRequestModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        const start = dayjs(this._data.booking.date);
        const end = start.add(this._data.booking.duration, 'm');
        this.booking = {
            id: null,
            title: this._data.booking.title,
            room: this._data.booking.room[0],
            date: this._data.booking.date,
            organiser: (this._data.booking as any).host,
            duration: this._data.booking.duration,
            attendees: this._data.booking.attendees,
            conferencing: (this._data.booking as any).conferencing,
            display: {
                date: start.format('DD/MM/YYYY'),
                long_date: start.format('DD MMMM YYYY'),
                time: `${start.format('h:mma')} - ${end.format('h:mma')}`,
            }
        } as any;
    }
}
