
import { Component, OnInit, Output, EventEmitter, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

import { DialogEvent, Identity, HashMap } from '../../shared/utilities/types.interfaces';
import { Utils } from '../../shared/utility.class';
import { AppService } from '../../services/app.service';
import { rulesForSpace } from '../../shared/utilities/booking.utilities';
import { IRoomAvailabilityRequest, IRoom } from '../../services/data/rooms.service';
import { BaseDirective } from '../../shared/globals/base.directive';
import { IBuilding } from '../../services/data/buildings.service';

import * as dayjs from 'dayjs';
import { IUser } from '../../services/data/users.service';

export interface RoomSelectModalData {
    /** Whether booking is all day */
    all_day: boolean;
    /** Unix epoch in ms of the event start time */
    date: number;
    /** Duration of the event in minutes */
    duration: number;
    /** List of spaces in the event */
    spaces: IRoom[];
    /** Host of the event */
    host?: IUser;
    /** Unix epoch in ms of the recurrence end date */
    recurr_end?: number;
    /** ID of the booking being edited */
    booking_id?: string;
    /** Whether booking has catering */
    has_catering?: boolean;
    /** Whether user is allowed to select multiple spaces */
    multiple_spaces?: boolean;
}

@Component({
    selector: 'room-select-modal',
    templateUrl: './room-select-modal.template.html',
    styleUrls: ['./room-select-modal.styles.scss'],
})
export class RoomSelectModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user actions on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** List of selected spaces */
    public space_list: IRoom[];
    /** List of available buildings */
    public buildings: IBuilding[];
    /** Index of the active building */
    public active_building: number;
    /** List of available room types */
    public room_types: Identity[];
    /** Index of the active room type */
    public active_type: number;
    /** Whether space data is loading */
    public loading: boolean;
    /** List of spaces available for event period */
    public results: IRoom[];
    /** Whether sidebar should be shown */
    public show: boolean;

    /** Whether user is allowed to select multiple spaces */
    public get multiple_spaces(): boolean {
        return !!this._data.multiple_spaces;
    }

    /** Currently active building */
    public get building(): IBuilding {
        return this.buildings[this.active_building || 0];
    }

    /** Currently active room type */
    public get type(): Identity {
        return this.room_types[this.active_type || 0];
    }

    /** Mapping of spaces to whether they are selected */
    public get selected_spaces(): HashMap<boolean> {
        return (this.space_list || []).reduce((map, space) => {
            map[space.id] = true;
            return map;
        }, {});
    }

    constructor(
        private _dialog: MatDialogRef<RoomSelectModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: RoomSelectModalData,
        private _service: AppService
    ) {
        super();
    }

    public ngOnInit(): void {
        this.space_list = this._data.spaces && this._data.spaces.length ? [...this._data.spaces] : [];
        this.subscription('buildings', this._service.Buildings.listen((bld) => this.initLists()));
        this.initLists();
        this.updateList();
    }

    public initLists() {
        this.buildings = this._service.Buildings.list();
        const space: IRoom = this.space_list[0] || {} as any;
        const zone = (space.level || {} as any).bld_id || this._service.Buildings.current().id;
        this.active_building = this.buildings.findIndex(i => i.id === zone);

        const room_types = this._service.Buildings.organisation().space_types;
        this.room_types = Object.keys(room_types).map(i => ({ id: i, name: room_types[i] }));
        this.room_types.unshift({ id: -1, name: 'All Room Types' });
        this.active_type = 0;
    }

    public updateList() {
        this.loading = true;
        const zones = [this.building.id || this._service.Buildings.current().id];
        if (this.type.id !== -1) {
            zones.push(`${this.type.id}`);
        }
        const query: IRoomAvailabilityRequest = {
            date: this._data.all_day ? dayjs(this._data.date).startOf('d').valueOf() : this._data.date,
            duration: this._data.all_day ? 24 * 60 : (this._data.duration + (this._data.has_catering ? 15 : 0)),
            ignore: this._data.booking_id,
            hide_bookings: true,
            zone_ids: zones.reduce((a, i) => a += a ? ',' + i : i, '')
        };
        this._service.Rooms.available(query).then((list) => {
            this.results = list.filter(i => {
                const bld: any = this._service.Buildings.list().find(j => j.id === i.level.bld_id) || {};
                const rules = rulesForSpace({
                    user: this._data.host || this._service.Users.current(),
                    space: i,
                    time: query.date,
                    recurr_end: this._data.recurr_end,
                    duration: query.duration,
                    rules: bld.booking_rules
                });
                // Check booking rules
                let valid_rules = true;
                if (rules) {
                    valid_rules = !rules.hide;
                }
                i.book_type = !rules.auto_approve ? 'Request' : 'Book';
                return valid_rules && zones.reduce((a, v) => a && i.zones.indexOf(v) >= 0, true)
            });
            this.results.sort((a, b) => a.level.name.localeCompare(b.level.name) || a.name.localeCompare(b.name));
            this.loading = false;
        }, _ => this.loading = false);
    }

    public select(item) {
        if (this._data.multiple_spaces) {
            if (!this.space_list) { this.space_list = []; }
            item.selected = true;
            this.space_list.push(item);
            this.space_list = Utils.unique(this.space_list, 'id');
        } else {
            this.space_list = [item];
            this.event.emit({ reason: 'done' });
        }
    }

    public remove(item) {
        if (this.space_list) {
            const index = this.space_list.findIndex(i => i.id === item.id);
            if (index >= 0) {
                this.space_list.splice(index, 1);
            }
        }
    }

    public finished() {
        this.event.emit({ reason: 'done' });
    }
}
